import { Box, Grid, IconButton, ImageList, ImageListItem, ImageListItemBar, Typography, useMediaQuery, useTheme } from "@mui/material";
import SortIcon from '@mui/icons-material/Sort';
import SubdirectoryArrowRightIcon from '@mui/icons-material/SubdirectoryArrowRight';
import VolunteerActivismIcon from '@mui/icons-material/VolunteerActivism';
import InfoIcon from '@mui/icons-material/Info';
import Image1 from '../../assets/images/projectImages/special_school/Img1.jpg';
import Image2 from '../../assets/images/projectImages/special_school/Img2.jpg';

import SchoolImage1 from '../../assets/images/schoolgalleryImage/Img1.jpg';
import SchoolImage2 from '../../assets/images/schoolgalleryImage/Img2.jpg';
import SchoolImage3 from '../../assets/images/schoolgalleryImage/Img3.jpg';
import SchoolImage4 from '../../assets/images/schoolgalleryImage/Img4.jpg';
import SchoolImage5 from '../../assets/images/schoolgalleryImage/Img5.jpg';
import SchoolImage6 from '../../assets/images/schoolgalleryImage/Img6.jpg';


export default function SpecialSchool() {

    const points = [
        "When we talk of rehabilitation, we don’t mean only economic rehabilitation. Rehabilitation is a much broader term. It includes medical rehabilitation, social rehabilitation and vocational & economic rehabilitation.",
        "When a child first comes to Navjeevan, the Clinical Psychologist and special educators assess him. His IQ is determined through clinical tests and he is assessed using various checklists. His case history is taken and if he is diagnosed to be mentally handicapped, he is admitted in the School or Vocational Training Center (VTC) depending on his age. In case of a children below 6yrs. of age, Home Training Programs are given. Medical Intervention, if necessary, is advised and they are referred to concerned professionals.",
        "The teachers again evaluate his level on basis of different checklists and prepare his Individual Educational Plan (IEP) in consultation with the parents.(IEP is prepared on basis of priorities as to which skills need to be tackled first or which problem needs to be tackled first.) The goals are set and the child is evaluated monthly, quarterly and half yearly. Any changes in the IEP, if felt needed are made."
    ]
    const itemData = [
        {
            img: SchoolImage1,
        },
        {
            img: SchoolImage2,
        },
        {
            img: SchoolImage3,
        },
        {
            img: SchoolImage4,
        },
        {
            img: SchoolImage5,
        },
        {
            img: SchoolImage6,
        },
    ]

    const theme = useTheme();
    const isSmScreen = useMediaQuery(theme.breakpoints.down('sm'));
    const isMdScreen = useMediaQuery(theme.breakpoints.up('md'));

    return (
        <>

            <Box sx={{ mt: 15 }} mx={{ md: 5, xs: 2 }}>
                <Box display={"flex"} alignItems={"center"} justifyContent={"center"} color={'#009CFF'}>
                    <SortIcon sx={{ transform: 'scaleX(-1)' }} />
                    <Typography sx={{
                        fontFamily: "Jost,sans-serif",
                        fontWeight: 'bold',
                        textTransform: 'uppercase',
                        marginX: 2,
                    }}>
                        School
                    </Typography>
                    <SortIcon />
                </Box>

                <Typography sx={{
                    textAlign: "center",
                    fontFamily: "Jost,sans-serif",
                    fontWeight: 'bold',
                    fontSize: {
                        xs: "2rem",
                        sm: "2.5rem"
                    }
                }}>
                    The Special School
                </Typography>

                <Grid container overflow="hidden" alignItems={"center"} pt={5}>
                    <Grid item xs={12} md={5} textAlign={"center"} alignSelf={"center"} mt={5}>
                        <Box component={"div"} sx={{
                            position: 'relative',
                            display: 'flex',
                            justifyContent: 'center',
                            alignItems: 'center',
                        }}>
                            <Box component={"div"} sx={{
                                position: 'absolute',
                                top: '-10%', // Adjust the positioning as needed
                                left: '1%', // Adjust the positioning as needed
                                width: '80%', // 80% image width + 2 * 5% padding
                                height: '95%', // 80% image height + 2 * 5% padding
                                border: '4px solid #009CFF', // Adjust the border color and thickness
                                zIndex: -1,
                                borderRadius: 2
                            }}>
                            </Box>
                            <img src={Image1} width="80%" height="80%" alt="" srcset="" style={{ borderRadius: 10 }} />
                        </Box>
                    </Grid>
                    <Grid item xs={12} md={7} mt={{ xs: 8, md: 0 }}>
                        <Box component={"div"} color={"silver"}>
                            <Typography variant="body1" style={{ display: 'flex', alignItems: 'center', fontFamily: "Jost,sans-serif" }}>
                                <span style={{ marginLeft: '0.5rem' }}>Training in daily living skills and survival skills is more important for these children than academic skills. So in School the training starts right from toilet training to identifying ones own bag, eating, grooming to socializing with classmates, identifying them by their names, developing work behavior by learning to sit in one place, to execute commands, concentrating on task at hand. Over a period, which could vary from 6 months to six years, depending on the child’s level, the child learns these skills.</span>
                            </Typography>
                        </Box>
                    </Grid>
                </Grid>

                <Grid container overflow="hidden" alignItems={"center"} pt={5}>
                    <Grid item xs={12} md={7} mt={{ xs: 8, md: 0 }} order={{ xs: 2, md: 1 }}>
                        <Box component={"div"} color={"silver"}>
                            <Typography variant="body1" style={{ display: 'flex', alignItems: 'center', fontFamily: "Jost,sans-serif" }}>
                                <span style={{ marginLeft: '0.5rem' }}>
                                    While being trained in daily living skills, children are also exposed to different sports, drawing, craft, music and dance. These activities act as multi-sensory stimulators. Children with academic aptitude take basic lessons in math and language.</span>
                            </Typography>
                        </Box>
                    </Grid>
                    <Grid item xs={12} md={5} textAlign={"center"} alignSelf={"center"} mt={5} pl={{ md: 1, xs: 0 }} order={{ xs: 1, md: 2 }}>
                        <Box component={"div"} sx={{
                            position: 'relative',
                            display: 'flex',
                            justifyContent: 'center',
                            alignItems: 'center',
                        }}>
                            <Box component={"div"} sx={{
                                position: 'absolute',
                                top: '-10%', // Adjust the positioning as needed
                                left: '1%', // Adjust the positioning as needed
                                width: '80%', // 80% image width + 2 * 5% padding
                                height: '95%', // 80% image height + 2 * 5% padding
                                border: '4px solid #009CFF', // Adjust the border color and thickness
                                zIndex: -1,
                                borderRadius: 2
                            }}>
                            </Box>
                            <img src={Image2} width="80%" height="80%" alt="" srcset="" style={{ borderRadius: 10 }} />
                        </Box>
                    </Grid>
                </Grid>


                <ImageList sx={{ mt: 15 }} cols={isMdScreen ? 2 : isSmScreen ? 1 : 2}>
                    {itemData.map((item, index) => (
                        <ImageListItem key={index}>
                            <img
                                srcSet={`${item.img}?w=248&fit=crop&auto=format&dpr=2 2x`}
                                src={`${item.img}?w=248&fit=crop&auto=format`}
                                loading="lazy"
                            />
                        </ImageListItem>
                    ))}
                </ImageList>
            </Box>

            <Box component={"div"} sx={{ mt: 15 }} mx={{ md: 5, xs: 2 }}>
                <Box display={"flex"} alignItems={"center"} justifyContent={"center"} color={'#009CFF'}>
                    <SortIcon sx={{ transform: 'scaleX(-1)' }} />
                    <Typography sx={{
                        fontFamily: "Jost,sans-serif",
                        fontWeight: 'bold',
                        textTransform: 'uppercase',
                        marginX: 2,
                    }}>
                        Rehabilitation
                    </Typography>
                    <SortIcon />
                </Box>

                <Typography sx={{
                    textAlign: "center",
                    fontFamily: "Jost,sans-serif",
                    fontWeight: 'bold',
                    fontSize: {
                        xs: "2rem",
                        sm: "2.5rem"
                    }
                }}>
                    Process of Rehabilitation
                </Typography>

                <Box component={"div"} color={"silver"}>
                    {
                        points.map((point) => (
                            <Typography variant="body1" style={{ display: 'flex', alignItems: 'center', fontFamily: "Jost,sans-serif", marginTop: '1rem' }}>
                                <SubdirectoryArrowRightIcon />
                                <span style={{ marginLeft: '0.5rem' }}>{point}</span>
                            </Typography>
                        ))
                    }
                </Box>
            </Box>


        </>
    )
}