import { Box, Grid, ThemeProvider, Typography, createTheme } from "@mui/material";
import ProjectCard from "./projectCard/ProjectCard";
import "@fontsource/dancing-script/600.css";
import earlyinterventionImage from '../../assets/images/projectLogoImages/earlyintervention.png';
import earlyinterventionImage1 from '../../assets/images/projectLogoImages/earlyintervention1.png';
import specialschoolImage from '../../assets/images/projectLogoImages/specialschool.png';
import specialschoolImage1 from '../../assets/images/projectLogoImages/specialschool1.png';
import trainingworkshopImage from '../../assets/images/projectLogoImages/traning-workshop.png';
import trainingworkshopImage1 from '../../assets/images/projectLogoImages/traning-workshop1.jpg';
import caregroupImage from '../../assets/images/projectLogoImages/care-group.png';
import caregroupImage1 from '../../assets/images/projectLogoImages/care-group1.png';
import TherapyImage from '../../assets/images/projectLogoImages/therapy.png';
import TherapyImage1 from '../../assets/images/projectLogoImages/therapy1.png';
import ResidentialImage from '../../assets/images/projectLogoImages/residential.png';
import ResidentialImage1 from '../../assets/images/projectLogoImages/residential1.png';
import ManPowerImage from '../../assets/images/projectLogoImages/manpower.png';
import ManPowerImage1 from '../../assets/images/projectLogoImages/manpower1.png';
import SortIcon from '@mui/icons-material/Sort';
import routes from "../../constants/routes";

const theme = createTheme({
    breakpoints: {
        values: {
            xs: 0,
            sm: 600, // Default: 600px
            md: 850, // Default: 960px
            lg: 1100, // Default: 1280px
            xl: 1920, // Default: 1920px
        },
    },
    // Other customizations can be added here, such as palette, typography, etc.
});


export default function Projects() {
    return (
        <>
            <ThemeProvider theme={theme}>
                <Box component={"div"} mt={17}  mx={{ md: 5, xs: 2 }}>

                    {/* Title */}
                    <Box component={"div"}>
                        <Box display={"flex"} alignItems={"center"} justifyContent={"center"}  color={'#009CFF'}>
                            <SortIcon sx={{ transform: 'scaleX(-1)' }} />
                            <Typography  variant="h6" component="h6" mx={2} sx={{
                                textAlign: "center",
                                fontFamily: "Jost,sans-serif",
                                fontWeight: 'bold',
                                textTransform: 'uppercase',
                            }}>
                                Our Projects
                            </Typography>
                            <SortIcon />
                        </Box>

                        <Typography sx={{
                            textAlign: "center",
                            fontFamily: "Jost,sans-serif",
                            fontWeight: 'bold',
                            fontSize: {
                                xs: "2rem",
                                lg: "2.5rem"
                            }
                        }} gutterBottom>
                            Learn More About Our Projects
                        </Typography>
                    </Box>

                    
                    {/* Programs of Navjeevan */}
                    <Grid container spacing={5} mt={2}>

                        {/* Early Invention */}
                        <Grid item xs={12} sm={6} md={4} lg={3}>
                            <ProjectCard title={"Early Intervention Center"} link={routes.EARLYINTERVENTION}
                                Image={earlyinterventionImage}
                            />
                        </Grid>

                        {/* Special School */}
                        <Grid item xs={12} sm={6} md={4} lg={3}>
                            <ProjectCard title={"Special School"} link={routes.SPECIALSCHOOL}
                                Image={specialschoolImage}
                            />
                        </Grid>

                        {/* Workshop */}
                        <Grid item xs={12} sm={6} md={4} lg={3}>
                            <ProjectCard title={"Vocational Traning & Shelter Workshop"} link={routes.SHELTEREDCARE}
                                Image={trainingworkshopImage}
                            />
                        </Grid>

                        {/* Care Group */}
                        <Grid item xs={12} sm={6} md={4} lg={3}>
                            <ProjectCard title={"Care Group"} link={routes.CAREGROUP}
                                Image={caregroupImage}
                            />
                        </Grid>

                        {/* Physiotherapy Center */}
                        <Grid item xs={12} sm={6} md={4} lg={3}>
                            <ProjectCard title={"Physiotherapy Center"} link={routes.THERAPIES}
                                Image={TherapyImage}
                            />
                        </Grid>

                        {/* Residental Care */}
                        <Grid item xs={12} sm={6} md={4} lg={3}>
                            <ProjectCard title={"Residental Care"} link={routes.RESIDENTALCARE}
                                Image={ResidentialImage}
                            />
                        </Grid>

                        {/* Manpower Development Program */}
                        <Grid item xs={12} sm={6} md={4} lg={3}>
                            <ProjectCard title={"Manpower Development Programs"} link={routes.OTHERPROGRAMS}
                                Image={ManPowerImage}
                            />
                        </Grid>
                    </Grid>

                </Box>
            </ThemeProvider>
        </>
    )
}