import { Cloud, ContentCopy, ContentCut, ContentPaste, Logout, PersonAdd, Settings } from "@mui/icons-material";
import { Avatar, Box, ClickAwayListener, Divider, Grow, ListItemIcon, ListItemText, Menu, MenuItem, MenuList, Paper, Popper, Typography } from "@mui/material";
import headerStyles from './headerStyles';
import { useNavigate } from "react-router-dom";
import { useEffect } from "react";

export default function MenuBar({ menu, setMenu, menuItems, handleMenuMouseEnter, handleMenuMouseLeave, handleCloseMenuAnchorEl }) {

    const navigate = useNavigate();

    const navigateToLink = (link) => {
        navigate(link);
    }

    return (
        <>
            <Popper
                open={Boolean(menu.anchorEl) || menu.hovered}
                anchorEl={menu.anchorEl}
                role={undefined}
                placement="bottom" // Adjust placement as needed
                transition
                disablePortal
                style={{
                    margin: '8px',
                    fontFamily: 'Montserrat, sans-serif', // Elegant Google Font
                    zIndex: 1,
                }}

                onMouseEnter={() => handleMenuMouseEnter(setMenu)}
                onMouseLeave={() => handleMenuMouseLeave(setMenu)}
                onMouseOut={() => handleCloseMenuAnchorEl(setMenu)}
            >
                {({ TransitionProps, placement }) => (
                    <Grow
                        {...TransitionProps}
                        style={{
                            transformOrigin: 'center top',
                            transition: 'transform 0.3s ease-in-out', // Example transition
                        }}
                        {...(Boolean(menu.anchorEl) ? { timeout: 1000 } : {})}
                    >
                        <Paper elevation={4} style={{
                            position: 'relative',
                            // borderRadius: '10px', // Decreased rounded corners
                            marginTop: '20px',
                            padding: '7px', // More spacious padding
                            paddingLeft: '15px',
                            width: '250px',
                            borderTop: '2px solid #2196f3',
                        }}>
                            <Box component="div" style={{
                                content: '""',
                                display: 'block',
                                position: 'absolute',
                                top: -2,
                                right: '48%',
                                width: 10,
                                height: 10,
                                backgroundColor: 'white',
                                borderTop: '2px solid #2196f3',
                                borderLeft: '2px solid #2196f3',
                                transform: 'translateY(-50%) rotate(45deg)',
                            }} />
                            <MenuList
                                id="composition-menu"
                                aria-labelledby="composition-button"
                            >
                                {
                                    menuItems.map(({ menuName, link }, index) => (
                                        <MenuItem
                                            key={index}
                                            sx={headerStyles.menuItemStyle}
                                            onClick={() => navigateToLink(link)}
                                        >
                                            {menuName}
                                        </MenuItem>
                                    ))
                                }
                            </MenuList>
                        </Paper>
                    </Grow>
                )}
            </Popper>
        </>
    )
}