import { Box, Card, Grid, Paper, Typography } from "@mui/material";
import FormatQuoteIcon from '@mui/icons-material/FormatQuote';
import PersonIcon from '@mui/icons-material/Person';

export default function ReviewCard({ name, post, text, color = 0 }) {

    const colors = [
        "red",
        "lightblue",
        "green",
        "orange",
        "purple"
    ]

    return (
        <>
            <Box component={"div"} display={"inline-block"} mt={3}>

                {/* Upper Card */}
                <Paper sx={{
                    width: "310px",
                    height: "170px",
                    borderRadius: 15,
                    borderBottomRightRadius: 0,
                    position: 'absolute',
                    marginLeft: 7.5,
                    zIndex: 1,
                }} >
                    <Box component="div" padding={1} style={{
                        height: '100%',
                        display: 'flex',
                        flexDirection: 'column',
                        justifyContent: 'space-between',
                    }}>
                        <FormatQuoteIcon style={{
                            transform: 'rotate(180deg)',
                            fontSize: "30",
                            color: colors[color]
                        }} />
                        <Typography paddingLeft={2} paddingRight={2} style={{
                            fontFamily: 'emoji',
                            fontFamily: "Jost,sans-serif",
                            fontSize: '0.770rem'
                        }}>
                            {text}
                        </Typography>
                        <FormatQuoteIcon style={{
                            fontSize: "30",
                            transform: 'scaleX(-1) rotate(180deg)',
                            float: 'inline-end',
                            color: colors[color],
                            alignSelf: 'end'
                        }} />
                    </Box>
                </Paper>

                {/* Lower Card */}
                <Paper sx={{
                    width: "330px",
                    height: "210px",
                    borderRadius: 15,
                    backgroundColor: colors[color],
                    marginTop: 8,
                    marginRight: 10,
                    marginLeft: 3,
                    transform: 'skewX(-15deg)',
                }} >
                    <Box component="div" style={{
                        transform: 'skewX(15deg)',
                        height: '100%',
                        marginBottom: 10,
                    }}>
                        <Grid container spacing={2} style={{
                            position: 'absolute',
                            bottom: 0,
                        }}>
                            <Grid item xs={4}>
                                <Box component="div" className="circle" style={{
                                    width: '100px',
                                    height: '100px',
                                    borderRadius: '50%',
                                    backgroundColor: 'white',
                                    color: colors[color],
                                    display: 'flex',
                                    justifyContent: 'center',
                                    alignItems: 'center',
                                    fontSize: '24px',
                                    marginRight: 25
                                }}>
                                    <PersonIcon style={{
                                        fontSize: 60
                                    }} />
                                </Box>
                            </Grid>
                            <Grid item xs={7}>
                                <Box component="div" style={{
                                    alignSelf: 'center',
                                    color: 'white',
                                    height: '100px',
                                    display: 'flex',
                                    flexDirection: 'column',
                                    justifyContent: 'center',
                                    marginTop: 10
                                }}>
                                    <Typography sx={{ fontSize: '1.15rem', textTransform: 'uppercase', fontWeight: 'bold' }}>
                                        {name}
                                    </Typography>
                                    <Typography variant="body2" sx={{ textTransform: 'uppercase' }}>
                                        {post}
                                    </Typography>
                                </Box>
                            </Grid>
                        </Grid>
                    </Box>
                </Paper>
            </Box>
        </>
    )
}