import { Carousel } from "react-responsive-carousel";
import 'react-responsive-carousel/lib/styles/carousel.min.css'
import Image1 from '../../assets/images/homeImages/bg2.jpg';
import Image2 from '../../assets/images/homeImages/bg3.jpg';
import Image3 from '../../assets/images/homeImages/bg8.jpg';
import Image4 from '../../assets/images/homeImages/bg7.jpg';
import Image5 from '../../assets/images/homeImages/bg5.jpg';
import Image6 from '../../assets/images/homeImages/bg6.jpg';


import { Box } from "@mui/material";


export default function ImageCarousel() {

    return (
        <>
            <Box component="div" mt={7}>
                <Carousel infiniteLoop={true} emulateTouch={true} showStatus={false} showArrows={false} swipeable={true} showThumbs={false} autoPlay>

                    {/* Image 1 */}
                        <img src={Image1} style={{ minHeight: '30rem' }} />
                    
                    {/* Image 2 */}
                        <img src={Image2} style={{ minHeight: '30rem' }} />
                    
                    {/* Image 3 */}
                        <img src={Image3} style={{ minHeight: '30rem' }} />
                    
                    {/* Image 4 */}
                        <img src={Image4} style={{ minHeight: '30rem' }} />
                    
                    {/* Image 5 */}
                    <Box>
                        <img src={Image5} style={{ minHeight: '30rem' }} />
                    </Box>

                    {/* Image 6 */}
                    <Box>
                        <img src={Image6} style={{ minHeight: '30rem' }} />
                    </Box>
                </Carousel>
            </Box>
        </>
    )
}