import { Box, IconButton, ImageList, ImageListItem, ImageListItemBar, ListSubheader, Typography, useMediaQuery, useTheme } from "@mui/material";
import SortIcon from '@mui/icons-material/Sort';
import InfoIcon from '@mui/icons-material/Info';
import SubdirectoryArrowRightIcon from '@mui/icons-material/SubdirectoryArrowRight';
import HomeTrainingImage1 from '../../assets/images/projectImages/home-training.jpg';
import HomeTrainingImage2 from '../../assets/images/projectImages/home-training.jpg';
import HostelInterior from '../../assets/images/projectImages/residentcare-1.jpg';
import HostelExterior from '../../assets/images/projectImages/hostel.jpg';


export default function ResidentalCare() {

    const points = [
        "To give the mentally challenged persons from villages around Aurangabad, an opportunity to be trained.",
        "To give relief to aging parents of such persons.",
        "To provide respite care in case of some domestic emergencie.",
        "To conduct residential camps for children"
    ];

    const points1 = [
        "Balanced Diet on the advice of the Nutritionist.(4 meals a day)",
        "Daily Exercise",
        "Weekly visit of a Doctor",
        "Regular services of barber",
        "Free admission to Navjeevan Vocational Training Center or Care Group"
    ]

    const points2 = [
        "Adult men trained in self help skills",
        "He should have his UDID card",
        "Our doctor should consider him fit for admission"
    ];

    const itemData = [
        {
            img: HomeTrainingImage1,
            title: 'Home Training',
        },
        {
            img: HomeTrainingImage2,
            title: 'Home Training',
        },
        {
            img: HostelInterior,
            title: 'Hostel Interior',
        },
        {
            img: HostelExterior,
            title: 'Hostel Exterior',
        },

    ]

    const theme = useTheme();
    const isSmScreen = useMediaQuery(theme.breakpoints.down('sm'));
    const isMdScreen = useMediaQuery(theme.breakpoints.up('md'));

    return (
        <>
            <Box component={"div"} mt={15} mx={{ md: 5, xs: 2 }}>
                <Box display={"flex"} alignItems={"center"} justifyContent={"center"} color={'#009CFF'}>
                    <SortIcon sx={{ transform: 'scaleX(-1)' }} />
                    <Typography sx={{
                        fontFamily: "Jost,sans-serif",
                        fontWeight: 'bold',
                        textTransform: 'uppercase',
                        marginX: 2,
                    }}>
                        Care
                    </Typography>
                    <SortIcon />
                </Box>
                <Typography sx={{
                    textAlign: "center",
                    fontFamily: "Jost,sans-serif",
                    fontWeight: 'bold',
                    fontSize: {
                        xs: "2rem",
                        sm: "2.5rem"
                    }
                }}>
                    Residental Care Unit
                </Typography>

                {/* Purpose */}
                <Typography mt={5} sx={{
                    fontFamily: "Jost,sans-serif",
                    fontWeight: 'bold',
                    fontSize: {
                        xs: "2rem"
                    }
                }}>
                    Purpose:
                </Typography>

                <Box component={"div"} color={"silver"}>
                    {
                        points.map((point) => (
                            <Typography variant="body1" style={{ display: 'flex', alignItems: 'center', fontFamily: "Jost,sans-serif", marginTop: '1rem' }}>
                                <SubdirectoryArrowRightIcon />
                                <span style={{ marginLeft: '0.5rem' }}>{point}</span>
                            </Typography>
                        ))
                    }
                </Box>

                {/* Facilities Provided: */}
                <Typography sx={{
                    fontFamily: "Jost,sans-serif",
                    fontWeight: 'bold',
                    mt: 5,
                    fontSize: {
                        xs: "2rem"
                    }
                }}>
                    Facilities Provided:
                </Typography>

                <Box component={"div"} color={"silver"}>
                    {
                        points1.map((point) => (
                            <Typography variant="body1" style={{ display: 'flex', alignItems: 'center', fontFamily: "Jost,sans-serif", marginTop: '1rem' }}>
                                <SubdirectoryArrowRightIcon />
                                <span style={{ marginLeft: '0.5rem' }}>{point}</span>
                            </Typography>
                        ))
                    }
                </Box>

                {/* Eligibility For Admission: */}
                <Typography sx={{
                    fontFamily: "Jost,sans-serif",
                    fontWeight: 'bold',
                    mt: 5,
                    fontSize: {
                        xs: "2rem"
                    }
                }}>
                    Eligibility For Admission:
                </Typography>

                <Box component={"div"} color={"silver"}>
                    {
                        points2.map((point) => (
                            <Typography variant="body1" style={{ display: 'flex', alignItems: 'center', fontFamily: "Jost,sans-serif", marginTop: '1rem' }}>
                                <SubdirectoryArrowRightIcon />
                                <span style={{ marginLeft: '0.5rem' }}>{point}</span>
                            </Typography>
                        ))
                    }
                </Box>


                <Box display={"flex"} alignItems={"center"} justifyContent={"center"} color={'#009CFF'} mt={10}>
                    <SortIcon sx={{ transform: 'scaleX(-1)' }} />
                    <Typography sx={{
                        fontFamily: "Jost,sans-serif",
                        fontWeight: 'bold',
                        textTransform: 'uppercase',
                        marginX: 2,
                    }}>
                        Training Program
                    </Typography>
                    <SortIcon />
                </Box>
                <Typography sx={{
                    textAlign: "center",
                    fontFamily: "Jost,sans-serif",
                    fontWeight: 'bold',
                    fontSize: {
                        xs: "2rem",
                        sm: "2.5rem"
                    }
                }}>
                    Home Training Program
                </Typography>

                <Box component={"div"} my={5} color={"silver"}>
                    <Typography variant="body1" style={{ display: 'flex', alignItems: 'center', fontFamily: "Jost,sans-serif", marginTop: '1rem' }}>
                        <span style={{ marginLeft: '0.5rem' }}>
                            Parents of children below 6 yrs of age who cannot be admitted to the school are given home training programs by our teachers and therapists. Same facility is extended to other children who find it difficult to come to school every day for different reasons.
                        </span>
                    </Typography>
                </Box>

                <ImageList sx={{ mt: 5 }} cols={isMdScreen ? 2 : isSmScreen ? 1 : 2}>
                    {itemData.map((item) => (
                        <ImageListItem key={item.img}>
                            <img
                                srcSet={`${item.img}?w=248&fit=crop&auto=format&dpr=2 2x`}
                                src={`${item.img}?w=248&fit=crop&auto=format`}
                                alt={item.title}
                                loading="lazy"
                            />
                            <ImageListItemBar
                                title={item.title}
                                actionIcon={
                                    <IconButton
                                        sx={{ color: 'rgba(255, 255, 255, 0.54)' }}
                                        aria-label={`info about ${item.title}`}
                                    >
                                        <InfoIcon />
                                    </IconButton>
                                }
                            />
                        </ImageListItem>
                    ))}
                </ImageList>
            </Box>
        </>
    )
}