import { Box, Button, Card, Container, Grid, Paper, Typography } from "@mui/material";
import BG1 from '../../assets/images/backgroundImages/bg-1.jpg';
import SubdirectoryArrowRightIcon from '@mui/icons-material/SubdirectoryArrowRight';
import SortIcon from '@mui/icons-material/Sort';
import VolunteerActivismIcon from '@mui/icons-material/VolunteerActivism';

export default function HelpUs() {

    const points = [
        "Donations in Cash or by Cheque for general purpose.",
        "Adoption of a child in School or VTC for one year i.e. bearing the fees, conveyance expenses and uniform expenses (Rs.17,000/-p.a.)",
        "Financial Adoption of a child in Residential Care Unit for any period (@ Rs.11,000 p.m.)",
        "Sponsoring different projects taken up by Navjeevan (by making a contribution to the corpus fund)",
        "Sponsoring the Maintenance Expenses of the premises for any period. (@Rs. 15,000 p.m.)",
        "Sponsoring the pay of 3 extra teachers employed (@ Rs.12,000/- p.m. per teacher)",
        "Sponsoring the pay of 4 extra care takers employed (@ Rs.6500/- p.m.per employee)",
        "Sponsoring the salary of security guard (@ 8000 p.m.)",
        "Annadan- Sponsoring the meals for residential care unit for one day (@ Rs.4100 ) ( to celebrate an occasion or remembering your dear ones )"
    ]

    return (
        <>
            <Box component={"div"} mt={12} mx={{ md: 5, xs: 2 }}>
                <Grid container overflow="hidden" alignItems={"center"} pt={10}>

                    <Grid item xs={12} md={6} textAlign={"center"} alignSelf={"center"} mt={5}>
                        <Box component={"div"} sx={{
                            position: 'relative',
                            display: 'flex',
                            justifyContent: 'center',
                            alignItems: 'center',

                        }}>
                            <Box component={"div"} sx={{
                                position: 'absolute',
                                top: '-10%', // Adjust the positioning as needed
                                left: '1%', // Adjust the positioning as needed
                                width: '80%', // 80% image width + 2 * 5% padding
                                height: '95%', // 80% image height + 2 * 5% padding
                                border: '4px solid #009CFF', // Adjust the border color and thickness
                                zIndex: -1,
                                borderRadius: 2
                            }}>
                            </Box>
                            <img src={BG1} width="80%" height="80%" alt="" srcset="" style={{ borderRadius: 10 }} />

                        </Box>
                    </Grid>

                    <Grid item xs={12} md={6} mt={{ xs: 10, md: 0 }}>

                        {/* Title */}
                        <Box component={"div"}>
                            <Box display={"flex"} alignItems={"center"} color={'#009CFF'}>
                                <SortIcon sx={{ transform: 'scaleX(-1)' }} />
                                <Typography variant="h6" component="h6" mx={2} sx={{
                                    textAlign: "center",
                                    fontFamily: "Jost,sans-serif",
                                    fontWeight: 'bold',
                                    textTransform: 'uppercase',
                                }}>
                                    Help Us
                                </Typography>
                                <SortIcon />
                            </Box>

                            <Typography sx={{
                                fontFamily: "Jost,sans-serif",
                                fontWeight: 'bold',
                                fontSize: {
                                    xs: "2rem",
                                    sm: "2.5rem"
                                }
                            }} gutterBottom>
                                How can you help us?
                            </Typography>
                        </Box>

                        <Box component={"div"} color={"silver"}>
                            {
                                points.map((point, index) => (
                                    <Typography key={index} variant="body1" style={{ display: 'flex', alignItems: 'center', fontFamily: "Jost,sans-serif", marginTop: '1rem' }}>
                                        <SubdirectoryArrowRightIcon />
                                        <span style={{ marginLeft: '0.5rem' }}>{point}</span>
                                    </Typography>
                                ))
                            }
                        </Box>

                        <Button disableElevation variant="contained" startIcon={<VolunteerActivismIcon />} sx={{
                            my: 5,
                            padding: 2,
                            borderRadius: 10,
                            color: 'white',
                            backgroundColor: '#009CFF',
                            fontFamily: 'Jost, sans-serif',
                            fontWeight: 700,
                            transition: 'background-color 0.3s, transform 0.3s',
                            '&:hover': {
                                backgroundColor: '#0077CC', // Change the background color on hover
                                transform: 'scale(1.05)', // Add a slight scale effect on hover
                            },
                        }}>
                            Donate Now
                        </Button>
                    </Grid>
                </Grid>
            </Box>
        </>
    )
}