import { Box, IconButton, ImageList, ImageListItem, ImageListItemBar, Typography, useMediaQuery, useTheme } from "@mui/material";
import SortIcon from '@mui/icons-material/Sort';
import SubdirectoryArrowRightIcon from '@mui/icons-material/SubdirectoryArrowRight';
import FiberManualRecordIcon from '@mui/icons-material/FiberManualRecord';
import InfoIcon from '@mui/icons-material/Info';
import OutReachProgramImage from '../../assets/images/projectImages/outreach.jpg';
import SportsImage1 from '../../assets/images/projectImages/sports1.jpg';
import SportsImage2 from '../../assets/images/projectImages/sports2.jpg';
import SportsImage3 from '../../assets/images/projectImages/sports3.jpg';
import SportsImage4 from '../../assets/images/projectImages/sports4.jpg';
import SportsImage5 from '../../assets/images/projectImages/sports5.jpg';
import SportsImage6 from '../../assets/images/projectImages/sports6.jpg';
import DrawingCraftImage from '../../assets/images/projectImages/drawingcraft.jpg';

export default function OtherPrograms() {

    const points = [
        "Foundation Course for school teachers, under Sarva Shikshan Abhiyan (Indira Gandhi National Open University,recognised by Rehabilitation Council of India – RCI).",
        "CRE courses for Special Teachers (recognised by RCI)",
        "Care Givers Course (recognised by RCI)"
    ]

    const points1 = [
        "Street plays",
        "Interaction with students of normal Schools and Colleges on Raksha Bandhan Day, Navratri and Christmas. We encourage schools and colleges to visit Navjeevan to expose their students to the subject of disability.",
        "Divyang Run",
        "Cultural Programs"
    ]

    const itemData = [
        {
            img: OutReachProgramImage,
            title: 'OutReach Program',
        },
        {
            img: SportsImage1,
            title: 'Sport and Cultural Activities',
        },
        {
            img: SportsImage2,
            title: 'Sport and Cultural Activities',
        },
        {
            img: SportsImage3,
            title: 'Sport and Cultural Activities',
        },
        {
            img: SportsImage4,
            title: 'Sport and Cultural Activities',
        },
        {
            img: SportsImage5,
            title: 'Sport and Cultural Activities',
        },
        {
            img: SportsImage6,
            title: 'Sport and Cultural Activities',
        },
        {
            img: DrawingCraftImage,
            title: 'Drawing & Craft',
        },
    ]


    const theme = useTheme();
    const isSmScreen = useMediaQuery(theme.breakpoints.down('sm'));
    const isMdScreen = useMediaQuery(theme.breakpoints.up('md'));

    return (
        <>
            <Box component={"div"} mt={15} mx={{ md: 5, xs: 2 }}>

                <Box display={"flex"} alignItems={"center"} justifyContent={"center"} color={'#009CFF'}>
                    <SortIcon sx={{ transform: 'scaleX(-1)' }} />
                    <Typography sx={{
                        fontFamily: "Jost,sans-serif",
                        fontWeight: 'bold',
                        textTransform: 'uppercase',
                        marginX: 2,
                    }}>
                        Programs
                    </Typography>
                    <SortIcon />
                </Box>

                <Typography sx={{
                    textAlign: "center",
                    fontFamily: "Jost,sans-serif",
                    fontWeight: 'bold',
                    fontSize: {
                        xs: "2rem",
                        sm: "2.5rem"
                    }
                }}>
                    Other Programs
                </Typography>


                <Typography sx={{
                    fontFamily: "Jost,sans-serif",
                    fontWeight: 'bold',
                    mt: 5,
                    fontSize: {
                        xs: "1.5rem"
                    }
                }}>
                    Manpower Development
                </Typography>

                <Box component={"div"} color={"silver"}>
                    {
                        points.map((point) => (
                            <Typography variant="body1" style={{ display: 'flex', alignItems: 'center', fontFamily: "Jost,sans-serif", marginTop: '1rem' }}>
                                <SubdirectoryArrowRightIcon />
                                <span style={{ marginLeft: '0.5rem' }}>{point}</span>
                            </Typography>
                        ))
                    }
                </Box>

                <Typography sx={{
                    fontFamily: "Jost,sans-serif",
                    fontWeight: 'bold',
                    mt: 5,
                    fontSize: {
                        xs: "1.5rem"
                    }
                }}>
                    Outreach Program
                </Typography>

                <Box component={"div"} color={"silver"}>
                    <Typography variant="body1" style={{ display: 'flex', alignItems: 'center', fontFamily: "Jost,sans-serif", marginTop: '1rem' }}>
                        <SubdirectoryArrowRightIcon />
                        <span style={{ marginLeft: '0.5rem' }}>
                            Screening slums and villages for mentally challenged persons and giving Home Training Programs.
                        </span>
                    </Typography>
                </Box>

                <Typography sx={{
                    fontFamily: "Jost,sans-serif",
                    fontWeight: 'bold',
                    mt: 5,
                    fontSize: {
                        xs: "1.5rem"
                    }
                }}>
                    Sport and Cultural Activities
                </Typography>

                <Box component={"div"} color={"silver"}>
                    <Typography variant="body1" style={{ display: 'flex', alignItems: 'center', fontFamily: "Jost,sans-serif", marginTop: '1rem' }}>
                        <SubdirectoryArrowRightIcon />
                        <span style={{ marginLeft: '0.5rem' }}>
                            In school as well as at the Local, District and State level.The students of Navjeevan participate in Local, State and National Level Sports and Cultural Activities. Such activities build up their self-esteem and confidence.
                        </span>
                    </Typography>
                </Box>

                <Typography sx={{
                    fontFamily: "Jost,sans-serif",
                    fontWeight: 'bold',
                    mt: 5,
                    fontSize: {
                        xs: "1.5rem"
                    }
                }}>
                    Community Awareness Programs
                </Typography>

                <Box component={"div"} color={"silver"}>
                    <Typography variant="body1" style={{ display: 'flex', alignItems: 'center', fontFamily: "Jost,sans-serif", marginTop: '1rem' }}>
                        <SubdirectoryArrowRightIcon />
                        <span style={{ marginLeft: '0.5rem' }}>
                            Interaction with schools of normal children, articles in local newspapers, talks on local radio etc.
                        </span>
                    </Typography>
                    <Typography variant="body1" style={{ display: 'flex', alignItems: 'center', fontFamily: "Jost,sans-serif", marginTop: '1rem' }}>
                        <SubdirectoryArrowRightIcon />
                        <span style={{ marginLeft: '0.5rem', fontWeight: 'bold' }}>
                            Events Are Organized To Sensitive The Community On The Issue Of Intellectual Disability.
                        </span>
                    </Typography>
                    <Box component={"div"} ml={3}>
                        {
                            points1.map((point) => (
                                <Typography variant="body1" style={{ display: 'flex', alignItems: 'center', fontFamily: "Jost,sans-serif", marginTop: '1rem' }}>
                                    <FiberManualRecordIcon fontSize="1" />
                                    <span style={{ marginLeft: '0.5rem' }}>{point}</span>
                                </Typography>
                            ))
                        }
                    </Box>
                </Box>

                <Typography sx={{
                    fontFamily: "Jost,sans-serif",
                    fontWeight: 'bold',
                    mt: 5,
                    fontSize: {
                        xs: "1.5rem"
                    }
                }}>
                    Workshops and Seminars
                </Typography>

                <Box component={"div"} color={"silver"}>
                    <Typography variant="body1" style={{ display: 'flex', alignItems: 'center', fontFamily: "Jost,sans-serif", marginTop: '1rem' }}>
                        <SubdirectoryArrowRightIcon />
                        <span style={{ marginLeft: '0.5rem' }}>
                            For teachers to upgrade themselves and for parents to make them aware of various legislations and other issues related to retardation.
                        </span>
                    </Typography>
                </Box>

                <ImageList sx={{ mt: 5 }} cols={isMdScreen ? 3 : isSmScreen ? 1 : 2}>
                    {itemData.map((item) => (
                        <ImageListItem key={item.img}>
                            <img
                                srcSet={`${item.img}?w=248&fit=crop&auto=format&dpr=2 2x`}
                                src={`${item.img}?w=248&fit=crop&auto=format`}
                                alt={item.title}
                                loading="lazy"
                            />
                            <ImageListItemBar
                                title={item.title}
                                actionIcon={
                                    <IconButton
                                        sx={{ color: 'rgba(255, 255, 255, 0.54)' }}
                                        aria-label={`info about ${item.title}`}
                                    >
                                        <InfoIcon />
                                    </IconButton>
                                }
                            />
                        </ImageListItem>
                    ))}
                </ImageList>
            </Box>
        </>
    )
}