import { Box, Typography } from "@mui/material";
import { ChartsXAxis, PieArcLabel, PieChart, pieArcLabelClasses } from "@mui/x-charts";
import SortIcon from '@mui/icons-material/Sort';

export default function Chart() {

    const data = [
        { id: 0, value: 20, label: 'Administration', color: '#0088FE' },
        { id: 1, value: 45, label: 'Educational', color: '#00C49F' },
        { id: 2, value: 7, label: 'Teaching Aids', color: '#FFBB28' },
        { id: 3, value: 13, label: 'Manpower Development', color: '#FF8042' },
        { id: 4, value: 15, label: 'Maintenance' },
    ];

    const getArcLabel = (params) => {
        return `${(params.value).toFixed(0)}%`;
    };


    return (
        <>
            <Box component={"div"} mt={17} justifyContent={"center"}>

                {/* Title */}
                <Box component={"div"}>
                    <Box display={"flex"} alignItems={"center"} justifyContent={"center"} color={'#009CFF'}>
                        <SortIcon sx={{ transform: 'scaleX(-1)' }} />
                        <Typography variant="h6" component="h6" mx={2} sx={{
                            textAlign: "center",
                            fontFamily: "Jost,sans-serif",
                            fontWeight: 'bold',
                            textTransform: 'uppercase',
                        }}>
                            Where the money goes
                        </Typography>
                        <SortIcon />
                    </Box>

                    <Typography sx={{
                        textAlign: "center",
                        fontFamily: "Jost,sans-serif",
                        fontWeight: 'bold',
                        fontSize: {
                            xs: "2rem",
                            lg: "2.5rem"
                        }
                    }} gutterBottom>
                        Allocation of Funds
                    </Typography>
                </Box>

                <PieChart
                    series={[
                        {
                            data,
                            highlightScope: { faded: 'global', highlighted: 'item' },
                            faded: { innerRadius: 30, additionalRadius: -30, color: 'gray' },
                            arcLabel: getArcLabel,
                        },
                    ]}
                    sx={{
                        [`& .${pieArcLabelClasses.root}`]: {
                            fill: 'white',
                            fontSize: 16,
                            fontWeight: 'bold',
                        },
                    }}

                    margin={{ left: 70, bottom: 70 }} // Adjust bottom margin to create space for the legend
                    height={400}
                    slotProps={{
                        legend: {
                            direction: 'row', // Change this to 'column' for vertical layout
                            position: { vertical: 'bottom', horizontal: 'middle' },
                            spacing: 20, // Increase the spacing between legend items
                        },
                    }}
                >
                </PieChart>
            </Box>
        </>
    )
}