import { Box, ImageList, ImageListItem, ImageListItemBar, Typography } from "@mui/material";
import SortIcon from '@mui/icons-material/Sort';
import Image1 from '../../assets/images/schoolgalleryImage/Img1.jpg';
import Image2 from '../../assets/images/schoolgalleryImage/Img2.jpg';
import Image3 from '../../assets/images/schoolgalleryImage/Img3.jpg';
import Image4 from '../../assets/images/schoolgalleryImage/Img14.jpg';
import Image5 from '../../assets/images/schoolgalleryImage/Img5.jpg';
import Image6 from '../../assets/images/schoolgalleryImage/Img6.jpg';

import Image7 from '../../assets/images/schoolgalleryImage/Img15.jpg';
import Image8 from '../../assets/images/schoolgalleryImage/Img8.jpg';
import Image9 from '../../assets/images/schoolgalleryImage/Img9.jpg';
import Image10 from '../../assets/images/schoolgalleryImage/Img10.jpg';
import Image11 from '../../assets/images/schoolgalleryImage/Img11.jpg';
import Image12 from '../../assets/images/schoolgalleryImage/Img12.jpg';
import Image13 from '../../assets/images/schoolgalleryImage/Img13.jpg';

import Image15 from '../../assets/images/schoolgalleryImage/Img7.jpg';
import Image14 from '../../assets/images/schoolgalleryImage/Img4.jpg';
import Image16 from '../../assets/images/schoolgalleryImage/Img16.jpg';


function srcset(image, width, height, rows = 1, cols = 1) {
    return {
        src: `${image}?w=${width * cols}&h=${height * rows}&fit=crop&auto=format`,
        srcSet: `${image}?w=${width * cols}&h=${height * rows
            }&fit=crop&auto=format&dpr=2 2x`,
    };
}

export default function SchoolGallery() {

    const itemData = [
        {
            img: Image1,
            title: 'Breakfast',
            featured: true,
        },
        {
            img: Image2,
            title: 'Burger',
        },
        {
            img: Image3,
            title: 'Camera',
        },
        {
            img: Image4,
            title: 'Coffee',
            featured: true,
        },
        {
            img: Image5,
            title: 'Hats',
        },
        {
            img: Image6,
            title: 'Honey',
        },
        {
            img: Image7,
            title: 'Basketball',
            featured: true,
        },
        {
            img: Image8,
            title: 'Fern',
        },
        {
            img: Image9,
            title: 'Mushrooms',
        },
        {
            img: Image10,
            title: 'Tomato basil',
            featured: true,
        },
        {
            img: Image11,
            title: 'Sea star',
        },
        {
            img: Image12,
            title: 'Bike',
        },
        {
            img: Image13,
            title: 'Bike',
            featured: true,
        },
        {
            img: Image14,
            title: 'Bike',
        },
        {
            img: Image15,
            title: 'Bike',
        },
        {
            img: Image16,
            title: 'Bike',
            featured: true,
        }
    ];

    return (
        <>
            <Box component={"div"} mt={15} mx={{sx: 2, md: 10}}>
                <Box display={"flex"} alignItems={"center"} justifyContent={"center"} color={'#009CFF'}>
                    <SortIcon sx={{ transform: 'scaleX(-1)' }} />
                    <Typography sx={{
                        fontFamily: "Jost,sans-serif",
                        fontWeight: 'bold',
                        textTransform: 'uppercase',
                        marginX: 2,
                    }}>
                        Gallery
                    </Typography>
                    <SortIcon />
                </Box>
                <Typography sx={{
                    textAlign: "center",
                    fontFamily: "Jost,sans-serif",
                    marginBottom: '30px',
                    fontWeight: 'bold',
                    fontSize: {
                        xs: "2rem",
                        lg: "3rem"
                    }
                }}>
                    School Gallery
                </Typography>


                <ImageList
                    sx={{
                        // Promote the list into its own layer in Chrome. This costs memory, but helps keeping high FPS.
                        transform: 'translateZ(0)',
                    }}
                    gap={5}
                >
                    {itemData.map((item) => {
                        const cols = item.featured ? 2 : 1;
                        const rows = item.featured ? 2 : 1;

                        return (
                            <ImageListItem key={item.img} cols={cols} rows={rows}>
                                <img
                                    {...srcset(item.img, 250, 200, rows, cols)}
                                    alt={item.title}
                                    loading="lazy"
                                />
                                {/* <ImageListItemBar
                                    sx={{
                                        background:
                                            'linear-gradient(to bottom, rgba(0,0,0,0.7) 0%, ' +
                                            'rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)',
                                    }}
                                    title={item.title}
                                    position="top"
                                    actionIcon={
                                        <IconButton
                                            sx={{ color: 'white' }}
                                            aria-label={`star ${item.title}`}
                                        >
                                            <StarBorderIcon />
                                        </IconButton>
                                    }
                                    actionPosition="left"
                                /> */}
                            </ImageListItem>
                        );
                    })}
                </ImageList>
            </Box>
        </>
    )
}