import { Box, Container, Grid, Typography } from "@mui/material";
import Image1 from '../../assets/images/aboutUsImages/Img3.jpg';
import SortIcon from '@mui/icons-material/Sort';
import SubdirectoryArrowRightIcon from '@mui/icons-material/SubdirectoryArrowRight';
import Image2 from '../../assets/images/aboutUsImages/Img4.jpg';
import TeamImg from '../../assets/images/aboutUsImages/team.jpg';



export default function AboutUs() {

    const points = [
        "Navjeevan Society for Research & Rehabilitation of the Mentally Retarded, a registered Society, was established on 5th September 1985, in the city of Aurangabad, with a primary goal of training and supporting the mentally challenged persons.",
        "Aurangabad is a city in Maharashtra State of India, 410 km. from Mumbai. World famous for its archaeological wonders of Ajanta and Ellora caves, Aurangabad is considered to be the fastest growing city in Asia. Having a population of 15,87,000, the percentage of population.with mental retardation has been discovered to be an alarming 2%. As against this, the number of institutions working for the mentally challenged is negligible."
    ]

    const points1 = [
        "Navjeevan Society for Research & Rehabilitation of the Mentally Retarded, a registered Society, was established on 5th September 1985, in the city of Aurangabad, with a primary goal of training and supporting the mentally challenged persons.Mrs. Nalini Shah, President and founder member of Navjeevan Society, has a nephew who is mentally challenged.",
        "Back in the 1960’s Aurangabad was not very developed and there was no awareness about mental retardation. With the encouragement and guidance of Dr. Kumbhakarna, Dr. Ambadas Ambulgekar (both paediatricians) and Dr. Vinay Barhale (psychiatrist), Navjeevan was founded on 5th Sept 1985 with only 18 children in a rented place.",
        "The school was first of its kind in Aurangabad ditrict. In its early phase the school was more of a crèche than a special education centre. But the leadership was dynamic. Mrs. Jayshree Kendrekar joined Navjeevan family as a trustee and administrator.Today there are in all 175 students under the roof of Navjeevan."
    ]

    return (
        <>
            <Box component={"div"}>
                <img src={Image1} width={"100%"} height={"500px"} alt="" srcset="" />
                <Box component={"div"} sx={{ mt: 5 }} mx={{ md: 5, xs: 2 }}>
                    <Box component={"div"}>
                        {/* Title */}
                        <Box display={"flex"} alignItems={"center"} justifyContent={"center"} color={'#009CFF'}>
                            <SortIcon sx={{ transform: 'scaleX(-1)' }} />
                            <Typography sx={{
                                fontFamily: "Jost,sans-serif",
                                fontWeight: 'bold',
                                textTransform: 'uppercase',
                                marginX: 2,
                            }}>
                                About Us
                            </Typography>
                            <SortIcon />
                        </Box>
                        <Typography sx={{
                            textAlign: "center",
                            fontFamily: "Jost,sans-serif",
                            fontWeight: 'bold',
                            fontSize: {
                                xs: "2rem",
                                sm: "2.5rem"
                            }
                        }}>
                            About Navjeevan Society
                        </Typography>
                        <Box component={"div"} my={5} color={"silver"}>
                            {
                                points.map((point) => (
                                    <Typography variant="body1" style={{ display: 'flex', alignItems: 'center', fontFamily: "Jost,sans-serif", marginTop: '1rem' }}>
                                        <SubdirectoryArrowRightIcon />
                                        <span style={{ marginLeft: '0.5rem' }}>{point}</span>
                                    </Typography>
                                ))
                            }
                        </Box>
                    </Box>
                </Box>
            </Box>

            <Box component={"div"} mt={5} mx={{ md: 5, xs: 2 }}>
                <Grid container overflow="hidden" pt={10}>
                    <Grid item xs={12} md={7} mt={{ xs: 8, md: 0 }} order={{ xs: 2, md: 1 }}>
                        {/* Title */}
                        <Box component={"div"}>
                            <Box display={"flex"} alignItems={"center"} color={'#009CFF'}>
                                <SortIcon sx={{ transform: 'scaleX(-1)' }} />
                                <Typography variant="h6" component="h6" mx={2} sx={{
                                    textAlign: "center",
                                    fontFamily: "Jost,sans-serif",
                                    fontWeight: 'bold',
                                    textTransform: 'uppercase',
                                }}>
                                    History
                                </Typography>
                                <SortIcon />
                            </Box>

                            <Typography sx={{
                                fontFamily: "Jost,sans-serif",
                                fontWeight: 'bold',
                                fontSize: {
                                    xs: "2rem",
                                    sm: "2.5rem"
                                }
                            }} gutterBottom>
                                History of Navjeevan Society
                            </Typography>
                        </Box>

                        <Box component={"div"} my={5} color={"silver"}>
                            {
                                points1.map((point) => (
                                    <Typography variant="body1" style={{ display: 'flex', alignItems: 'center', fontFamily: "Jost,sans-serif", marginTop: '1rem' }}>
                                        <SubdirectoryArrowRightIcon />
                                        <span style={{ marginLeft: '0.5rem' }}>{point}</span>
                                    </Typography>
                                ))
                            }
                        </Box>
                    </Grid>
                    <Grid item xs={12} md={5} textAlign={"center"} alignSelf={"center"} mt={5} pl={{ md: 1, xs: 0 }} order={{ xs: 1, md: 2 }}>
                        <Box component={"div"} sx={{
                            position: 'relative',
                            display: 'flex',
                            justifyContent: 'center',
                            alignItems: 'center',
                        }}>
                            <Box component={"div"} sx={{
                                position: 'absolute',
                                top: '-10%', // Adjust the positioning as needed
                                left: '1%', // Adjust the positioning as needed
                                width: '80%', // 80% image width + 2 * 5% padding
                                height: '95%', // 80% image height + 2 * 5% padding
                                border: '4px solid #009CFF', // Adjust the border color and thickness
                                zIndex: -1,
                                borderRadius: 2
                            }}>
                            </Box>
                            <img src={Image2} width="80%" height="80%" alt="" srcset="" style={{ borderRadius: 10 }} />
                        </Box>
                    </Grid>
                </Grid>
            </Box>

            <Box component={"div"} sx={{ mt: 5 }} mx={{ md: 5, xs: 2 }} textAlign={"center"}>
                <Box component={"div"} mb={2}>
                    {/* Title */}
                    <Box display={"flex"} alignItems={"center"} justifyContent={"center"} color={'#009CFF'}>
                        <SortIcon sx={{ transform: 'scaleX(-1)' }} />
                        <Typography sx={{
                            fontFamily: "Jost,sans-serif",
                            fontWeight: 'bold',
                            textTransform: 'uppercase',
                            marginX: 2,
                        }}>
                            Team
                        </Typography>
                        <SortIcon />
                    </Box>
                    <Typography sx={{
                        textAlign: "center",
                        fontFamily: "Jost,sans-serif",
                        fontWeight: 'bold',
                        fontSize: {
                            xs: "2rem",
                            sm: "2.5rem"
                        }
                    }}>
                        Our Team
                    </Typography>
                </Box>
                <img src={TeamImg} width={"100%"} height={"80%"} alt="" srcset="" />
            </Box>
        </>
    )
}