import { Box, Collapse, List, ListItem, ListItemButton, ListItemText, MenuItem, Select, SwipeableDrawer, Typography } from "@mui/material";
import { useState } from "react";
import FiberManualRecordIcon from '@mui/icons-material/FiberManualRecord';
import { ExpandLess, ExpandMore } from "@mui/icons-material";
import routes from "../../../constants/routes";
import { useNavigate } from "react-router-dom";

const style = {
    listItemStyle: {
        color: 'white',
        fontFamily: "Jost,sans-serif",
        fontWeight: 'bold'
    },
    sideBarCollapseIconTheme: {
        color: 'white'
    }
}

export default function SlideDrawer({ drawerOpen, handleShowDrawer, handleHideDrawer }) {

    const navigate = useNavigate();

    const [aboutUsCollapse, setAboutUsCollapse] = useState(false);
    const aboutUsItems = [
        { menuName: 'Intellectual Disability', link: routes.INTELLECTUALDISABILITY },
        { menuName: 'About Us', link: routes.ABOUTUS },
        { menuName: 'Aims', link: routes.AIMS },
        { menuName: 'Achievements', link: routes.ACHIEVEMENTS },
        { menuName: 'Trustees & Patronst', link: routes.TRUSTEES },
        { menuName: 'Presidents Address', link: routes.PRESIDENTADDRESS },
    ];

    const [projectsCollapse, setProjectsCollapse] = useState(false);
    const projectsItems = [
        { menuName: 'Special School', link: routes.SPECIALSCHOOL },
        { menuName: 'Residential Care', link: routes.RESIDENTALCARE },
        { menuName: 'V Training & S Workshop', link: routes.SHELTEREDCARE },
        { menuName: 'Therapies', link: routes.THERAPIES },
        { menuName: 'Care Group', link: routes.CAREGROUP },
        { menuName: 'Early Intervention Center', link: routes.EARLYINTERVENTION },
        { menuName: 'Other Programs', link: routes.OTHERPROGRAMS },
    ];

    const [galleryCollapse, setGalleryCollapse] = useState(false);
    const galleryItems = [
        { menuName: 'School Gallery', link: routes.SCHOOLGALLERY },
        { menuName: 'Events Gallery', link: routes.EVENTSGALLERY },
    ]

    const handleAboutUsCollapse = () => {
        setAboutUsCollapse((prevProps) => !prevProps);
    };

    const handleProjectsCollapse = () => {
        setProjectsCollapse((prevProps) => !prevProps);
    };

    const handleGalleryCollapse = () => {
        setGalleryCollapse((prevProps) => !prevProps);
    };

    const navigateToLink = (link) => {
        navigate(link);
        handleHideDrawer();
    }

    const SubMenu = ({ menuName, link }) => {
        return (
            <>
                <ListItem disablePadding>
                    <ListItemButton onClick={() => navigateToLink(link)}>
                        <FiberManualRecordIcon style={{ fontSize: 10, marginRight: 10, color: "white" }} />
                        <ListItemText primary={menuName} primaryTypographyProps={style.listItemStyle} />
                    </ListItemButton>
                </ListItem>
            </>
        )
    }


    return (
        <>
            <SwipeableDrawer
                open={drawerOpen}
                onOpen={handleShowDrawer}
                onClose={handleHideDrawer}
            >
                <Box sx={{ height: '100%', minWidth: 250, bgcolor: "#009CFF" }} pt={2}>

                    <Typography style={{ textAlign: 'center', color: 'white', fontFamily: "Jost,sans-serif", fontWeight: '600', fontSize: '2.5rem' }}>
                        NavJeevan
                    </Typography>

                    <List>

                        {/* Home */}
                        <ListItem disablePadding>
                            <ListItemButton onClick={() => navigateToLink(routes.HOME)}>
                                <ListItemText primary="Home" primaryTypographyProps={style.listItemStyle} />
                            </ListItemButton>
                        </ListItem>

                        {/* About Us */}
                        <ListItem disablePadding>
                            <ListItemButton onClick={handleAboutUsCollapse}>
                                <ListItemText primary="About Us" primaryTypographyProps={style.listItemStyle} />
                                {aboutUsCollapse ? <ExpandLess sx={{ ...style.sideBarCollapseIconTheme }} /> : <ExpandMore sx={{ ...style.sideBarCollapseIconTheme }} />}
                            </ListItemButton>
                        </ListItem>

                        {/* Sub Menu of About Us */}
                        <Collapse in={aboutUsCollapse}>
                            <Box ml={2}>
                                {
                                    aboutUsItems.map((item, index) => (
                                        <SubMenu key={index} menuName={item.menuName} link={item.link} />
                                    ))
                                }
                            </Box>
                        </Collapse>

                        {/* Projects */}
                        <ListItem disablePadding>
                            <ListItemButton onClick={handleProjectsCollapse}>
                                <ListItemText primary="Projects" primaryTypographyProps={style.listItemStyle} />
                                {projectsCollapse ? <ExpandLess sx={{ ...style.sideBarCollapseIconTheme }} /> : <ExpandMore sx={{ ...style.sideBarCollapseIconTheme }} />}
                            </ListItemButton>
                        </ListItem>

                        {/* Sub Menu of Projects */}
                        <Collapse in={projectsCollapse}>
                            <Box ml={2}>
                                {
                                    projectsItems.map((item, index) => (
                                        <SubMenu key={index} menuName={item.menuName} link={item.link} />
                                    ))
                                }
                            </Box>
                        </Collapse>

                        {/* News & Events */}
                        <ListItem disablePadding>
                            <ListItemButton onClick={() => navigateToLink(routes.NEWSEVENTS)}>
                                <ListItemText primary="News & Events" primaryTypographyProps={style.listItemStyle} />
                            </ListItemButton>
                        </ListItem>

                        {/* Shop */}
                        <ListItem disablePadding>
                            <ListItemButton onClick={() => navigateToLink(routes.SHOP)}>
                                <ListItemText primary="Shop" primaryTypographyProps={style.listItemStyle} />
                            </ListItemButton>
                        </ListItem>

                        {/* Gallery */}
                        <ListItem disablePadding>
                            <ListItemButton onClick={handleGalleryCollapse}>
                                <ListItemText primary="Gallery" primaryTypographyProps={style.listItemStyle} />
                                {galleryCollapse ? <ExpandLess sx={{ ...style.sideBarCollapseIconTheme }} /> : <ExpandMore sx={{ ...style.sideBarCollapseIconTheme }} />}
                            </ListItemButton>
                        </ListItem>

                        {/* Sub Menu of Gallery */}
                        <Collapse in={galleryCollapse}>
                            <Box ml={2}>
                                {
                                    galleryItems.map((item, index) => (
                                        <SubMenu key={index} menuName={item.menuName} link={item.link} />
                                    ))
                                }
                            </Box>
                        </Collapse>

                        {/* Contact Us */}
                        <ListItem disablePadding>
                            <ListItemButton onClick={() => navigateToLink(routes.CONTACTUS)}>
                                <ListItemText primary="Contact Us" primaryTypographyProps={style.listItemStyle} />
                            </ListItemButton>
                        </ListItem>
                    </List>
                </Box>
            </SwipeableDrawer>
        </>
    )
}