import { Box, Card, CardActionArea, CardContent, CardMedia, ThemeProvider, Typography, createTheme } from '@mui/material';
import { useNavigate } from 'react-router-dom';

export default function ProjectCard({ title, link, Image, }) {

    const navigate = useNavigate();

    const navigateToLink = () => {
        navigate(link);
    }

    return (
        <>
            <Card onClick={navigateToLink} sx={{
                textAlign: '-webkit-center',
                position: 'relative', // Set position to relative for overlay positioning
                borderRadius: '12px',
                boxShadow: '0px 8px 16px rgba(0, 0, 0, 0.3)', // Stronger shadow for depth
                transition: 'transform 0.3s, box-shadow 0.3s', // Smooth transition for both transform and box-shadow
                '&:hover': {
                    transform: 'scale(1.05)', // Slightly larger scale on hover
                    boxShadow: '0px 12px 24px rgba(0, 0, 0, 0.4)', // Deeper shadow on hover
                },
            }}>
                <CardActionArea>
                    <CardMedia
                        component="img"
                        image={Image}
                        alt={title}
                        sx={{
                            padding: 2,
                            width: '70%',
                            objectFit: 'cover', // Maintain aspect ratio and cover the entire space
                            borderTopLeftRadius: '12px',
                            borderTopRightRadius: '12px',
                        }}
                    />
                    <CardContent sx={{
                        backgroundColor: '#009CFF',
                        color: 'white',
                        textAlign: 'center',
                        height: '50px'
                    }}>
                        <Typography gutterBottom variant="body1" component="div" sx={{
                            fontFamily: "Jost, sans-serif",
                            fontWeight: 'bold',
                            textTransform: 'uppercase',
                            letterSpacing: '1px', // Add letter-spacing for better readability
                        }}>
                            {title}
                        </Typography>
                    </CardContent>
                </CardActionArea>
            </Card>
        </>
    )
}