import { Box, Button, FormControl, Grid, InputLabel, OutlinedInput, TextField, Typography } from "@mui/material";
import SortIcon from '@mui/icons-material/Sort';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import EmailIcon from '@mui/icons-material/Email';
import PhoneIcon from '@mui/icons-material/Phone';

export default function ContactUs(params) {
    return (
        <>
            <Box component={"div"} mt={15} mx={{ md: 5, xs: 2 }}>
                <Box display={"flex"} alignItems={"center"} justifyContent={"center"} color={'#009CFF'}>
                    <SortIcon sx={{ transform: 'scaleX(-1)' }} />
                    <Typography sx={{
                        fontFamily: "Jost,sans-serif",
                        fontWeight: 'bold',
                        textTransform: 'uppercase',
                        marginX: 2,
                    }}>
                        Contact Us
                    </Typography>
                    <SortIcon />
                </Box>

                <Typography sx={{
                    textAlign: "center",
                    fontFamily: "Jost,sans-serif",
                    fontWeight: 'bold',
                    fontSize: {
                        xs: "2rem",
                        sm: "2.5rem"
                    }
                }}>
                    Contact Us
                </Typography>

                <Grid container spacing={2} mt={5}>
                    <Grid item xs={12} md={6}>
                        <iframe
                            title="Google Map"
                            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d6309.86035992527!2d75.36925819207663!3d19.887913810937608!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3bdba2e3aaaaaaab%3A0xed856ee8e7086d4f!2sNavjeevan+Society+for+Research+%26+Rehabilitation+of+the+Mentally+Retarded!5e0!3m2!1sen!2sin!4v1522481445209"
                            width="100%"
                            height="500"
                            style={{ border: '0px', width: '100%', height: '500px' }}
                            allowFullScreen=""
                        ></iframe>
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <Box
                            component="form"
                            sx={{
                                '& .MuiTextField-root': { m: 1 },
                            }}
                            noValidate
                            autoComplete="off"
                        >
                            <Typography sx={{
                                textAlign: "center",
                                fontFamily: "Jost,sans-serif",
                                fontWeight: 'bold',
                                fontSize: {
                                    xs: "2rem"
                                }
                            }}>
                                Contact Info
                            </Typography>

                            <FormControl fullWidth sx={{ m: 1 }}>
                                <InputLabel htmlFor="name">Name</InputLabel>
                                <OutlinedInput
                                    id="name"
                                    label="Name"
                                />
                            </FormControl>

                            <FormControl fullWidth sx={{ m: 1 }}>
                                <InputLabel htmlFor="email">Email</InputLabel>
                                <OutlinedInput
                                    id="email"
                                    label="Email"
                                />
                            </FormControl>

                            <FormControl fullWidth sx={{ m: 1 }}>
                                <InputLabel htmlFor="subject">Subject</InputLabel>
                                <OutlinedInput
                                    id="subject"
                                    label="Subject"
                                />
                            </FormControl>

                            <FormControl fullWidth sx={{ m: 1 }}>
                                <InputLabel htmlFor="message">Message</InputLabel>
                                <OutlinedInput
                                    id="message"
                                    label="Message"
                                    multiline
                                    rows={5}
                                />

                            </FormControl>

                            <FormControl fullWidth sx={{ m: 1 }}>
                                <Button variant="contained">Send Message</Button>
                            </FormControl>
                        </Box>
                    </Grid>
                </Grid>

                <Grid container spacing={2} mt={5}>
                    <Grid item xs={12} lg={4}>
                        <Box component={"div"} display={"flex"}>
                            <LocationOnIcon sx={{ fontSize: 60, color: '#009CFF' }} />
                            <Box>
                                <Typography variant="body1" fontFamily={"Jost,sans-serif"} fontWeight={"bold"}>
                                    Address :
                                </Typography>
                                <Typography variant="body2" fontFamily={"Jost,sans-serif"} color={"silver"}>
                                    Plot No. P 65, Naregaon Road, Chikalthana MIDC Area, MIDC Industrial Area, Chilkalthana, Aurangabad, Maharashtra 431210
                                </Typography>
                            </Box>
                        </Box>
                    </Grid>
                    <Grid item xs={12} md={6} lg={4}>
                        <Box component={"div"} display={"flex"}>
                            <EmailIcon sx={{ fontSize: 60, color: '#009CFF' }} />
                            <Box ml={1}>
                                <Typography variant="body1" fontFamily={"Jost,sans-serif"} fontWeight={"bold"}>
                                    Email :
                                </Typography>
                                <Typography variant="body2" fontFamily={"Jost,sans-serif"} color={"silver"}>
                                    navjeevanspecial@gmail.com<br />
                                    info@navjeevanspecial.com
                                </Typography>
                            </Box>
                        </Box>
                    </Grid>
                    <Grid item xs={12} md={6} lg={4}>
                        <Box component={"div"} display={"flex"}>
                            <PhoneIcon sx={{ fontSize: 60, color: '#009CFF' }} />
                            <Box ml={1}>
                                <Typography variant="body1" fontFamily={"Jost,sans-serif"} fontWeight={"bold"}>
                                    Phone :
                                </Typography>
                                <Typography variant="body2" fontFamily={"Jost,sans-serif"} color={"silver"}>
                                    9226925915<br />
                                    9923779772<br />
                                    9326222260
                                </Typography>
                            </Box>
                        </Box>
                    </Grid>
                </Grid>
            </Box>
        </>
    )
}