import { Box, Card, CardContent, CardMedia, Container, Grid, Typography } from "@mui/material";
import SortIcon from '@mui/icons-material/Sort';
import SimpleImage from '../../assets/images/productImages/simple-file.jpg';
import SpringImage from '../../assets/images/productImages/spring-file.jpg';
import PaperBagImage from '../../assets/images/productImages/paper-bag.jpg';
import MotiSupariImage from '../../assets/images/productImages/moti-supari.jpg';
import EnvelopeImage from '../../assets/images/productImages/jari-bag.jpg';
import GiftEnvelopeImage from '../../assets/images/productImages/gift-envelope.jpg';
import JariBagImage from '../../assets/images/productImages/jari-bag.jpg';
import DiwaliDiyaImage from '../../assets/images/productImages/diya.jpg';
import DusterImage from '../../assets/images/productImages/duster.jpg';
import WritingPadsImage from '../../assets/images/productImages/writing-pad.jpg';


export default function Shop() {

    const items = [
        { name: "SIMPLE FILE", price: 10, image: SimpleImage },
        { name: "SPRING FILE", price: 20, image: SpringImage },

        { name: "PAPER BAG", price: 25, image: PaperBagImage },
        { name: "MOTI SUPARI", price: 25, image: MotiSupariImage },

        { name: "ENVELOPE", price: 12.99, image: EnvelopeImage },
        { name: "GIFT ENVELOPE", price: 25, image: GiftEnvelopeImage },

        { name: "JARI BAG", price: 75, image: JariBagImage },
        { name: "DIWALI DIYA", price: 60, quantity: 6, image: DiwaliDiyaImage },

        { name: "DUSTER", price: 35, quantity: 3, image: DusterImage },
        { name: "WRITING PADS", price: 35, image: WritingPadsImage },
    ]

    return (
        <>
            <Box component={"div"} mt={15} mx={{ md: 5, xs: 2 }}>
                <Box display={"flex"} alignItems={"center"} justifyContent={"center"} color={'#009CFF'}>
                    <SortIcon sx={{ transform: 'scaleX(-1)' }} />
                    <Typography sx={{
                        fontFamily: "Jost,sans-serif",
                        fontWeight: 'bold',
                        textTransform: 'uppercase',
                        marginX: 2,
                    }}>
                        Shop
                    </Typography>
                    <SortIcon />
                </Box>
                <Typography sx={{
                    textAlign: "center",
                    fontFamily: "Jost,sans-serif",
                    fontWeight: 'bold',
                    fontSize: {
                        xs: "2rem",
                        sm: "2.5rem"
                    }
                }}>
                    Our Shop
                </Typography>


                <Container sx={{ py: 4 }} maxWidth="md">
                    {/* End hero unit */}
                    <Grid container spacing={4}>
                        {items.map((item, index) => (
                            <Grid item key={index} xs={12} sm={6} md={4}>
                                <Card
                                    sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}
                                >
                                    <CardMedia
                                        component="div"
                                        sx={{
                                            // 16:9
                                            pt: '56.25%',
                                        }}
                                        image={item.image}
                                    />
                                    <CardContent sx={{ flexGrow: 1 }}>
                                        <Typography gutterBottom variant="h5" component="h2" fontFamily="Jost,sans-serif" fontWeight={"bold"}>
                                            {item.name} {item?.quantity !== undefined && (
                                                <span style={{fontSize: '1rem'}}>(Pack of {item.quantity})</span>
                                            )}
                                        </Typography>
                                        <Typography gutterBottom variant="h5" component="h2" fontFamily="Jost,sans-serif" color={"#009CFF"} fontWeight={"bold"}>
                                            ₹{item.price}
                                        </Typography>
                                    </CardContent>
                                    {/* <CardActions>
                                        <Button size="small">View</Button>
                                        <Button size="small">Edit</Button>
                                    </CardActions> */}
                                </Card>
                            </Grid>
                        ))}
                    </Grid>
                </Container>
            </Box>
        </>
    )
}