const styles = {
    buttons: {
        color: '#009CFF',
        borderRadius: '10px', // Adding border radius to the button
        my: 2,
        fontFamily: "Jost,sans-serif",
        fontWeight: 900,
        '&:hover': {
            transition: 'background-color 0.3s ease-in-out, color 0.3s ease-in-out, box-shadow 0.3s ease-in-out',
            backgroundColor: '#2196f3', // Change to the desired color on hover (blue shade)
            color: '#ffffff', // Text color on hover
            boxShadow: '0px 3px 6px rgba(0, 0, 0, 0.1)', // Add a subtle shadow on hover
        }
    },
    hoverbuttons: {
        transition: 'background-color 0.3s ease-in-out, color 0.3s ease-in-out, box-shadow 0.3s ease-in-out',
        backgroundColor: '#2196f3', // Change to the desired color on hover (blue shade)
        color: '#ffffff', // Text color on hover
        boxShadow: '0px 3px 6px rgba(0, 0, 0, 0.1)', // Add a subtle shadow on hover
    },

    menuItemStyle: {
        
        color:"#009CFF",
        "&:hover":{
            fontWeight: '600',
            backgroundColor: 'transparent'
        }
    }
}

export default styles;