import { Box, Typography } from "@mui/material";
import ReviewCard from "./ReviewCard";
import Slider from "react-slick";
import SortIcon from '@mui/icons-material/Sort';

export default function Reviews() {


    const settings = {
        infinite: true,
        autoplay: true,
        speed: 1000,
        autoplaySpeed: 3000,
        dots: true,
        slidesToShow: 3,
        slidesToScroll: 1,
        initialSlide: 0,

        responsive: [{
            breakpoint: 1200,
            settings: {
                slidesToShow: 2,
                slidesToScroll: 1,
                infinite: true,
                autoplay: true,
                speed: 1000,
                autoplaySpeed: 3000,
                dots: true,
            }
        }, {
            breakpoint: 820,
            settings: {
                slidesToShow: 1,
                slidesToScroll: 1,
                infinite: true,
                autoplay: true,
                speed: 1000,
                autoplaySpeed: 3000,
                dots: true,
            }
        }],

    };

    const reviewsList = [
        { name: "CP Aurangabad", post: "", text: "I got an opportunity to visit Navjeevan School for mentally retarded children. No matter of what age of retardation.Excellent effort by Mrs.Nalini Shah to start this.", color: 0 },
        { name: "Kamal Shroff", post: "", text: "I am extremely impressed by the phenomenal progress the school has made.The dedication of Nalinitai and her collogues toward the school is admirable.", color: 1 },
        { name: "S. A. Nipankar", post: "", text: "An unique institution where the management and staff are working with utmost lead, devotion and dedication for the rehabilitation of the mentally retarded.", color: 2 },
        { name: "Mukund Bhogle", post: "", text: "It was great experience visiting this school. It is an example of how hard working and dedicated work can bring about social change or weaker section of society.", color: 3 },
        { name: "Spruha Joshi", post: "Actress, Poet,Lyricist.", text: "Wonderful experience. A child forever....I loved to be among the innocent children and dedicated Teachers and Staff.", color: 4 },
        { name: "NITIN .R PATIL", post: "COMISSIONER FOR PERSONS WITH  DISABLITY.", text: "Navjeevan is an inspiration for other schools of Maharashtra.Please keep up the GOOD work.", color: 0 },
        { name: "KAUSHAL INAMDAR", post: "Lyriscist", text: "Your campus has abundance of Love and Care and we could feel it in every classroom, workshop and face of every Teacher and Student.", color: 1 },
        { name: "OMPRAKASH SHETE", post: "SPECIAL OFFICER, CM OFFICE.", text: "NAVJEEVAN SCHOOL is epitome of Care, Love, Dedication and Social Awareness. The best school for special Children in Aurangabad.", color: 2 },
        { name: "SANDIP KULKARNI", post: "BIOLOGY TEACHER,KAZAKSTAN", text: "This School and the Teachers do an excellent job with smiles on their face and Love and Dedication in their hearts!! I look forward to getting associated more in the future with this organisation.", color: 3 },
        { name: "MANTRI", post: "", text: "Today on 14/10/2016, got a chance to visit the special school run by Navjeevan Society for Mentally Handicapped. The staff  is extremely Dedicated and Honest and hence the School has such good reputation. Heartfelt Good Wishes!!", color: 4 },
        { name: "VINAY V. POTWAY", post: "MANASHAKTI KENDRA, LONAVALA.", text: "Homely campus, Educated teachers, Proper class, dedicated staff, excellent patience!!  NAVJEEVAN SOCIETY you are doing a commendable job!!", color: 1 },
        { name: "Minister of Social Welfare", text: "Navjeevan is one of the best schools that i have seen. They are doing a great work of bringing light in the lives of these special children. Hence such good reputation.", color: 2 }
    ];

    return (
        <>
            <Box component={"div"} mt={17}>

                {/* Title */}
                <Box component={"div"}>
                    <Box display={"flex"} alignItems={"center"} justifyContent={"center"} color={'#009CFF'}>
                        <SortIcon sx={{ transform: 'scaleX(-1)' }} />
                        <Typography variant="h6" component="h6" mx={2} sx={{
                            textAlign: "center",
                            fontFamily: "Jost,sans-serif",
                            fontWeight: 'bold',
                            textTransform: 'uppercase',
                        }}>
                            Reviews
                        </Typography>
                        <SortIcon />
                    </Box>

                    <Typography sx={{
                        textAlign: "center",
                        fontFamily: "Jost,sans-serif",
                        fontWeight: 'bold',
                        fontSize: {
                            xs: "2rem",
                            lg: "2.5rem"
                        }
                    }} gutterBottom>
                        What Says Our Guest !
                    </Typography>
                </Box>


                <Box component="div" overflow="hidden" textAlign={"center"}>
                    <Slider {...settings}>
                        {
                            reviewsList.map((review, index) => (
                                <ReviewCard key={index} name={review.name} post={review.post} text={review.text}
                                    color={review.color}
                                />
                            ))
                        }
                    </Slider>
                </Box>
            </Box>
        </>
    )
}