import SortIcon from '@mui/icons-material/Sort';
import SubdirectoryArrowRightIcon from '@mui/icons-material/SubdirectoryArrowRight';
import VolunteerActivismIcon from '@mui/icons-material/VolunteerActivism';
import { Box, Button, Grid, Typography } from "@mui/material";
import Image1 from '../../assets/images/aboutUsImages/Img7.jpg';

export default function PresidentAddress() {

    const points = [
        "Dear All,",
        "The new management took over the reins of Navjeevan Society from the committee that founded Navjeevan. They had left no stone unturned to make Navjeevan a prestigious institution that it is today. Late Mrs.Nalini Shah, Dr.Vinay Barhale, Dr.Ambadas Ambulgekar, Mr.Keshav Deshpande and Mrs.Jayashree Kendrekar…They were a strong team dedicated to work for the intellectually disabled.I was fortunate to work with this team from the past 25 years.",
        "Over the years the term has changed from Mentally Retarded to Mentally Handicapped to Special Children and now to Intellectually Disabled. This indicates that globally, acceptance of the intellectually disabled has increased. We need to continue to put in our efforts for their rehabilitation, particularly their social acceptance and economic rehabilitation, wherever possible.",
        "The help we receive from the Corporate Sector and individuals is indispensable. They have continued to support all our projects over the years. We acknowledge their continuous support.",
        "The new management committee includes people from different fields. Together we are trying to put in our individual expertise to take Navjeevan to greater heights. In the past year, in spite of the pandemic, different projects were taken up. This academic year started with the official inauguration of the Early Intervention Center. Many more projects are lined up. Our primary goal this year is to strengthen the Sheltered Workshop. The vision is economic independence of our adults.",
        "I promise to see you in the next President’s address with many success stories!."
    ]

    return (
        <>
            <Box component={"div"} mt={15} mx={{ md: 5, xs: 2 }}>
                <Box display={"flex"} alignItems={"center"} justifyContent={"center"} color={'#009CFF'}>
                    <SortIcon sx={{ transform: 'scaleX(-1)' }} />
                    <Typography sx={{
                        fontFamily: "Jost,sans-serif",
                        fontWeight: 'bold',
                        textTransform: 'uppercase',
                        marginX: 2,
                    }}>
                        President
                    </Typography>
                    <SortIcon />
                </Box>
                <Typography sx={{
                    textAlign: "center",
                    fontFamily: "Jost,sans-serif",
                    fontWeight: 'bold',
                    fontSize: {
                        xs: "2rem",
                        sm: "2.5rem"
                    }
                }}>
                    President Address
                </Typography>

                <Grid container overflow="hidden" pt={5}>
                    <Grid item xs={12} md={5} textAlign={"center"} alignSelf={"center"} mt={5} pl={{ md: 1, xs: 0 }}>
                        <Box component={"div"} sx={{
                            position: 'relative',
                            display: 'flex',
                            justifyContent: 'center',
                            alignItems: 'center',
                        }}>
                            <Box component={"div"} sx={{
                                position: 'absolute',
                                top: '-10%', // Adjust the positioning as needed
                                left: '1%', // Adjust the positioning as needed
                                width: '80%', // 80% image width + 2 * 5% padding
                                height: '95%', // 80% image height + 2 * 5% padding
                                border: '4px solid #009CFF', // Adjust the border color and thickness
                                zIndex: -1,
                                borderRadius: 2
                            }}>
                            </Box>
                            <img src={Image1} width="80%" height="80%" alt="" srcset="" style={{ borderRadius: 10 }} />
                        </Box>
                    </Grid>
                    <Grid item xs={12} md={7}>
                        <Box component={"div"} my={5} color={"silver"}>
                            {
                                points.map((point,index) => (
                                    <Typography key={index} variant="body1" style={{ display: 'flex', alignItems: 'center', fontFamily: "Jost,sans-serif", marginTop: '1rem' }}>
                                        <span style={{ marginLeft: '0.5rem' }}>{point}</span>
                                    </Typography>
                                ))
                            }
                        </Box>
                    </Grid>
                </Grid>
            </Box>
        </>
    )
}