import SortIcon from '@mui/icons-material/Sort';
import SubdirectoryArrowRightIcon from '@mui/icons-material/SubdirectoryArrowRight';
import VolunteerActivismIcon from '@mui/icons-material/VolunteerActivism';
import { styled } from '@mui/material/styles';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import { Box, Typography } from '@mui/material';

const StyledTableCell = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
        backgroundColor: '#009CFF',
        color: theme.palette.common.white,
        fontFamily: "Jost,sans-serif",
        fontWeight: "bold",
        fontSize: 16,
    },
    [`&.${tableCellClasses.body}`]: {
        fontSize: 14,
        fontFamily: "Jost,sans-serif",
    },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
    '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover,
    },
    // hide last border
    '&:last-child td, &:last-child th': {
        border: 0,
    },
}));

function createData(name, designation, contactno) {
    return { name, designation, contactno };
}



export default function Trustees() {

    const trusteesList = [
        createData('Mrs. Sharmila Veerdhawal Gandhi', 'President', '9326222260'),
        createData('Dr. Chinmay Vinay Barhade', 'Vice President', '9823012123'),
        createData('Mr. Badriprasad Nandalal Rathi', 'Secretary', '9822060075'),
        createData('Mr. Sunil Jayraj Ved', 'Treasurer', '9423028028'),
        createData('Mr. Ramesh Panditrao Duse', 'Joint Secretary', '9422202747'),
        createData('Dr. Ramdas Marutirao Ambulgekar', 'Member', '9860059409'),
        createData('Mr. Shyam Omprakash Chhawchhariya', 'Member', '9890449132'),
        createData('Advocate Archana Vikas Gondhalekar', 'Member', '9922941685'),
        createData('Mrs. Shushma Shrikantrao Kulkarni', 'Member', '7798193666'),
    ];

    const points = [
        "MIDC (Maharashtra Industrial Development Corporation) of Aurangabad region has given a land of 2400 Sq. m on lease at a subsidised rate of Rs.10.76 per Sq. m for the cause.",
        "Action Aid had sponsored a Community Based Rehabilitation Program conducted in five villages.",
        "HPCL contributed to the growth of Navjeevan by constructing half of the Residential Care Unit building. The other half was constructed by Gupta Group of Industries.",
        "Mahyco Seeds Ltd.Built a building for the Sheltered Workshop.",
        "MIDC (Maharashtra Industrial Development Corporation) of Aurangabad region has given a land of 2400 Sq. m on lease at a subsidised rate of Rs.10.76 per Sq. m for the cause.",
        "Vibgyor Chemicals Pvt. Ltd. constructed the Vocational Training Centre building.",
        "Amway India’s Amway Opportunity Foundation helped set up the Physiotherapy Center by not only providing all the equipment but also bearing all expenses of the unit for 5 yrs (from 2008 to 2013)",
        "Fab Habitat, USA, has been sponsoring the operational expenses of the Physiotherapy Unit and some administrative expenses since 2015.",
        "State Bank of Hyderabad has presented a Maruti Omni Van for the purpose of transportation of school children.",
        "A local builder and a parent of our student, Mr.Malkhare, has gifted a 2 acre farming plot to the Society.",
        "Capital First Limited supported our Physio Therapy Center for 3 yrs.",
        "Fab Habitat, USA, helped us setup the Early Intervention Center and supported it and our Care Group Unit for 3 yrs.",
        "AEI-CIE, Aurangabad, has been sponsoring the operational expenses of the Physiotherapy Unit and some administrative expenses since 2015.",
        "Plus Tech, Pune, has supported our Care Group Unit for one year.",
        "Capital First Ltd, suppoted the Care Group fortwo consecutive years.",
        "UMA Sons Aurangabad - Garden play equipments.",
    ]

    return (
        <>
            <Box component={"div"} mt={15} mx={{ md: 5, xs: 2 }}>
                <Box display={"flex"} alignItems={"center"} justifyContent={"center"} color={'#009CFF'}>
                    <SortIcon sx={{ transform: 'scaleX(-1)' }} />
                    <Typography sx={{
                        fontFamily: "Jost,sans-serif",
                        fontWeight: 'bold',
                        textTransform: 'uppercase',
                        marginX: 2,
                    }}>
                        Trustees
                    </Typography>
                    <SortIcon />
                </Box>
                <Typography sx={{
                    textAlign: "center",
                    fontFamily: "Jost,sans-serif",
                    fontWeight: 'bold',
                    fontSize: {
                        xs: "2rem",
                        sm: "2.5rem"
                    }
                }}>
                    List Of Trustees
                </Typography>

                <Box component={"div"} mx={3} mt={4}>
                    <TableContainer component={Paper}>
                        <Table sx={{ me: 4 }}>
                            <TableHead>
                                <TableRow>
                                    <StyledTableCell>Name of Trustees</StyledTableCell>
                                    <StyledTableCell align="right">Designation</StyledTableCell>
                                    <StyledTableCell align="right">Contact No.</StyledTableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {trusteesList.map((row) => (
                                    <StyledTableRow key={row.name}>
                                        <StyledTableCell component="th" scope="row">
                                            {row.name}
                                        </StyledTableCell>
                                        <StyledTableCell align="right">{row.designation}</StyledTableCell>
                                        <StyledTableCell align="right">{row.contactno}</StyledTableCell>
                                    </StyledTableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </TableContainer>
                </Box>
            </Box>

            <Box component={"div"} mt={15} mx={{ md: 5, xs: 2 }}>
                <Box display={"flex"} alignItems={"center"} justifyContent={"center"} color={'#009CFF'}>
                    <SortIcon sx={{ transform: 'scaleX(-1)' }} />
                    <Typography sx={{
                        fontFamily: "Jost,sans-serif",
                        fontWeight: 'bold',
                        textTransform: 'uppercase',
                        marginX: 2,
                    }}>
                        Patrons
                    </Typography>
                    <SortIcon />
                </Box>
                <Typography sx={{
                    textAlign: "center",
                    fontFamily: "Jost,sans-serif",
                    fontWeight: 'bold',
                    fontSize: {
                        xs: "2rem",
                        sm: "2.5rem"
                    }
                }}>
                    Our Patrons
                </Typography>
                <Box component={"div"} my={5} color={"silver"}>
                    {
                        points.map((point, index) => (
                            <Typography key={index} variant="body1" style={{
                                display: 'flex', alignItems: 'center', fontFamily: "Jost,sans-serif", marginTop: '1rem',
                            }}>
                                <SubdirectoryArrowRightIcon />
                                <span style={{ marginLeft: '0.5rem' }}>{point}</span>
                            </Typography>
                        ))
                    }
                </Box>
            </Box>
        </>
    )
}