import VolunteerActivismIcon from '@mui/icons-material/VolunteerActivism';
import { Fab, Typography, Zoom } from "@mui/material";
import QRImage from '../../assets/images/QRcode.jpg';

export default function DonateButton() {

    const handleFabClick = () => {
        window.open(QRImage, '_blank'); // Opens the image in a new tab
    };

    return (
        <>
            <Fab variant="extended" sx={{
                position: 'fixed',
                bottom: 30,
                right: 30,
                backgroundColor:"#009CFF",
                color: 'white',
                fontFamily: 'Jost, sans-serif',
            }} aria-label={"Donate Button"} color="primary" onClick={handleFabClick}>
                <VolunteerActivismIcon sx={{ mr: 1 }} />
                Donate
            </Fab>
        </>
    )
}