import { LinearProgress } from "@mui/material";
import { useEffect, useState } from "react";

export default function Loading({ isLoading }) {
    const [progress, setProgress] = useState(0);

    useEffect(() => {
        let interval;

        if (isLoading) {
            const duration = 1000; // 5 seconds
            const step = (100 / duration) * 100; // Increment value for smoother animation

            interval = setInterval(() => {
                setProgress((prevProgress) => (prevProgress >= 100 ? 100 : prevProgress + step));
            }, 100); // Update every 100 milliseconds
        } else {
            // Reset progress when isLoading is false
            setProgress(0);
        }

        return () => {
            clearInterval(interval);
        };
    }, [isLoading]);

    return (
        <div style={{ position: 'fixed', top: 0, left: 0, right: 0, zIndex: 1000 }}>
            {isLoading && <LinearProgress variant="determinate" value={progress} />}
        </div>
    );
}