import AboutUs from "../components/about/AboutUs";
import Achievement from "../components/about/Achievement";
import Aims from "../components/about/Aims";
import IntellectualDisability from "../components/about/IntellectualDisability";
import PresidentAddress from "../components/about/PresidentAddress";
import Trustees from "../components/about/Trustees";
import ContactUs from "../components/contact/ContactUs";
import EventsGallery from "../components/gallery/EventsGallery";
import SchoolGallery from "../components/gallery/SchoolGallery";
import Home from "../components/home/Home";
import MainLayout from "../components/layout/MainLayout";
import NewsEvents from "../components/news/NewsEvents";
import CareGroup from "../components/projects/CareGroup";
import EarlyIntervention from "../components/projects/EarlyIntervention";
import OtherPrograms from "../components/projects/OtherPrograms";
import ResidentalCare from "../components/projects/ResidentalCare";
import ShelteredWorkshop from "../components/projects/ShelteredWorkshop";
import SpecialSchool from "../components/projects/SpecialSchool";
import Therapies from "../components/projects/Therapies";
import Shop from "../components/shop/Shop";

const mainRouter = {
    path: "/",
    Component: MainLayout,
    children: [
        { path: "", Component: Home },

        // About Us
        { path: "disability", Component: IntellectualDisability },
        { path: "aboutus", Component: AboutUs },
        { path: "aims", Component: Aims },
        { path: "achievements", Component: Achievement },
        { path: "trustees", Component: Trustees },
        { path: "presidentaddress", Component: PresidentAddress },

        // Projects
        { path: "specialschool", Component: SpecialSchool },
        { path: "residentalcare", Component: ResidentalCare },
        { path: "shelteredworkshop", Component: ShelteredWorkshop },
        { path: "therapies", Component: Therapies },
        { path: "caregroup", Component: CareGroup },
        { path: "earlyintervention", Component: EarlyIntervention },
        { path: "otherprograms", Component: OtherPrograms },

        // News&Events
        { path: "newsevents", Component: NewsEvents },
        { path: "shop", Component: Shop },

        // Gallery
        { path: "schoolgallery", Component: SchoolGallery },
        { path: "eventsgallery", Component: EventsGallery },

        // Contact Us
        
        { path: "contactus", Component: ContactUs },

    ],
};

export default mainRouter;