import { Box, Typography } from "@mui/material";
import SortIcon from '@mui/icons-material/Sort';
import SubdirectoryArrowRightIcon from '@mui/icons-material/SubdirectoryArrowRight';



export default function EarlyIntervention() {

    const points = [
        "To enhance the child’s normal development",
        "To help the child to develop to his/her fullest potential",
        "Creating a strong base for training at later stages",
        "To provide support and assistance to the family Timing of intervention becomes particularly important when a child runs the risk of missing an opportunity to learn during a state of maximum readiness. If the most teachable moments or stages of greatest readiness are not taken advantage of, a child may have difficulty in learning a particular skill at a later time."
    ];

    const points1 = [
        "Home Training Programs for 0-3 yrs.",
        "Classroom Programs with parents for 3-6 yrs."
    ]

    const points2 = [
        "Physical Development… including fine and gross motor function",
        "Cognitive Development",
        "Communication Development",
        "Social-Emotional Development or Adaptive Development."
    ];

    const points3 = [
        "Speech Therapy",
        "Occupational Therapy",
        "Physiotherapy",
        "Music Therapy",
        "Special Education",
        "Mental Health Program"
    ]

    return (
        <>
            <Box component={"div"} mt={15} mx={{ md: 5, xs: 2 }}>

                <Box display={"flex"} alignItems={"center"} justifyContent={"center"} color={'#009CFF'}>
                    <SortIcon sx={{ transform: 'scaleX(-1)' }} />
                    <Typography sx={{
                        fontFamily: "Jost,sans-serif",
                        fontWeight: 'bold',
                        textTransform: 'uppercase',
                        marginX: 2,
                    }}>
                        Intervention
                    </Typography>
                    <SortIcon />
                </Box>

                <Typography sx={{
                    textAlign: "center",
                    fontFamily: "Jost,sans-serif",
                    fontWeight: 'bold',
                    fontSize: {
                        xs: "2rem",
                        sm: "2.5rem"
                    }
                }}>
                    Early Intervention Services
                </Typography>

                <Typography mt={5} sx={{
                    fontFamily: "Jost,sans-serif",
                    fontWeight: 'bold',
                    fontSize: {
                        xs: "1rem"
                    }
                }}>
                    Early Intervention Services are effective ways to address the needs of infants and toddlers with developmental delays or disabilities. The primary reasons for intervening early with exceptional children are :
                </Typography>

                <Box component={"div"} color={"silver"}>
                    {
                        points.map((point, index) => (
                            <Typography key={index} variant="body1" style={{ display: 'flex', alignItems: 'center', fontFamily: "Jost,sans-serif", marginTop: '1rem' }}>
                                <SubdirectoryArrowRightIcon />
                                <span style={{ marginLeft: '0.5rem' }}>{point}</span>
                            </Typography>
                        ))
                    }
                </Box>

                <Typography sx={{
                    fontFamily: "Jost,sans-serif",
                    fontWeight: 'bold',
                    mt: 5,
                    fontSize: {
                        xs: "1rem"
                    }
                }}>
                    Interventions offered at Navjeevan Early Intervention Center:
                </Typography>

                <Box component={"div"} color={"silver"}>
                    {
                        points1.map((point, index) => (
                            <Typography key={index} variant="body1" style={{ display: 'flex', alignItems: 'center', fontFamily: "Jost,sans-serif", marginTop: '1rem' }}>
                                <SubdirectoryArrowRightIcon />
                                <span style={{ marginLeft: '0.5rem' }}>{point}</span>
                            </Typography>
                        ))
                    }
                </Box>

                <Typography sx={{
                    fontFamily: "Jost,sans-serif",
                    fontWeight: 'bold',
                    mt: 5,
                    fontSize: {
                        xs: "1rem"
                    }
                }}>
                    Our Aim……Achieving Holistic Development of the child.:
                </Typography>

                <Box component={"div"} color={"silver"}>
                    {
                        points2.map((point) => (
                            <Typography variant="body1" style={{ display: 'flex', alignItems: 'center', fontFamily: "Jost,sans-serif", marginTop: '1rem' }}>
                                <SubdirectoryArrowRightIcon />
                                <span style={{ marginLeft: '0.5rem' }}>{point}</span>
                            </Typography>
                        ))
                    }
                </Box>


                <Typography sx={{
                    fontFamily: "Jost,sans-serif",
                    fontWeight: 'bold',
                    mt: 5,
                    fontSize: {
                        xs: "1rem"
                    }
                }}>
                    Professionals use following therapies to achieve this goal:
                </Typography>

                <Box component={"div"} color={"silver"}>
                    {
                        points3.map((point) => (
                            <Typography variant="body1" style={{ display: 'flex', alignItems: 'center', fontFamily: "Jost,sans-serif", marginTop: '1rem' }}>
                                <SubdirectoryArrowRightIcon />
                                <span style={{ marginLeft: '0.5rem' }}>{point}</span>
                            </Typography>
                        ))
                    }
                </Box>
            </Box>
        </>
    )
}