import { Box, IconButton, ImageList, ImageListItem, ImageListItemBar, Typography, useMediaQuery, useTheme } from "@mui/material";
import SortIcon from '@mui/icons-material/Sort';
import InfoIcon from '@mui/icons-material/Info';
import TailorImage from '../../assets/images/projectImages/tailorImage.jpg';
import TailorImage1 from '../../assets/images/projectImages/tailorImage1.jpg';
import HomeScienceImage from '../../assets/images/projectImages/home-science.jpg';
import HomeScienceImage1 from '../../assets/images/projectImages/home-science1.jpg';
import FileSectionImage from '../../assets/images/projectImages/file-unit.jpg';
import FileSectionImage1 from '../../assets/images/projectImages/file-unit1.jpg';
import EnvelopImage from '../../assets/images/projectImages/Envelop.jpg';

export default function ShelteredWorkshop() {

    const itemData = [
        {
            img: TailorImage,
            title: 'Tailoring Section',
            subtitle: 'Aprons, Bed sheets, Pillow covers, Nappies which do not require much skill to stitch are made here.'
        },
        {
            img: TailorImage1,
            title: 'Tailoring Section',
            subtitle: 'Aprons, Bed sheets, Pillow covers, Nappies which do not require much skill to stitch are made here.'
        },
        {
            img: HomeScienceImage,
            title: 'Home Science',
            subtitle: 'The girls in this unit cook lunch for themselves everyday. They also make papad, chips, Moulded Chocolates, Chutneys, Cakes and cookies.'
        },
        {
            img: HomeScienceImage1,
            title: 'Home Science',
            subtitle: 'The girls in this unit cook lunch for themselves everyday. They also make papad, chips, Moulded Chocolates, Chutneys, Cakes and cookies.'
        },
        {
            img: FileSectionImage,
            title: 'File Section',
        },
        {
            img: FileSectionImage1,
            title: 'File Section',
        },
        {
            img: EnvelopImage,
            title: 'Envelope Section',
            subtitle: 'Envelopes are made from computer waste stationery. Supported by Dr. Hedgewar Hospital.'
        },
    ]

    const theme = useTheme();
    const isSmScreen = useMediaQuery(theme.breakpoints.down('sm'));
    const isMdScreen = useMediaQuery(theme.breakpoints.up('md'));

    return (
        <>
            <Box component={"div"} mt={15} mx={{ md: 5, xs: 2 }}>
                <Box display={"flex"} alignItems={"center"} justifyContent={"center"} color={'#009CFF'}>
                    <SortIcon sx={{ transform: 'scaleX(-1)' }} />
                    <Typography sx={{
                        fontFamily: "Jost,sans-serif",
                        fontWeight: 'bold',
                        textTransform: 'uppercase',
                        marginX: 2,
                    }}>
                        Workshop
                    </Typography>
                    <SortIcon />
                </Box>
                <Typography sx={{
                    textAlign: "center",
                    fontFamily: "Jost,sans-serif",
                    marginBottom: '30px',
                    fontWeight: 'bold',
                    fontSize: {
                        xs: "2rem",
                        sm: "2.5rem"
                    }
                }}>
                    Sheltered Workshop
                </Typography>

                <Box component={"div"} color={"silver"}>
                    <Typography variant="body1" style={{ display: 'flex', alignItems: 'center', fontFamily: "Jost,sans-serif", marginTop: '1rem' }}>
                        <span style={{ marginLeft: '0.5rem' }}>Navjeevan Sheltered Workshop (since 2000) – to give the adults enough work experience before getting job placement outside. File Unit, Tailoring Unit, Envelope Unit, Gardening Unit, Home Science Unit (papad making, chips making, chutney making, chocolate making etc.) Small profits made from sale of products are distributed as stipend among the trainee</span>
                    </Typography>
                </Box>

                <ImageList sx={{ mt: 5 }} cols={isMdScreen ? 3 : isSmScreen ? 1 : 2}>
                    {itemData.map((item) => (
                        <ImageListItem key={item.img}>
                            <img
                                srcSet={`${item.img}?w=248&fit=crop&auto=format&dpr=2 2x`}
                                src={`${item.img}?w=248&fit=crop&auto=format`}
                                alt={item.title}
                                loading="lazy"
                            />
                            <ImageListItemBar
                                title={item.title}
                                subtitle={item.subtitle}
                                actionIcon={
                                    <IconButton
                                        sx={{ color: 'rgba(255, 255, 255, 0.54)' }}
                                        aria-label={`info about ${item.title}`}
                                    >
                                        <InfoIcon />
                                    </IconButton>
                                }
                            />
                        </ImageListItem>
                    ))}
                </ImageList>
            </Box>
        </>
    )
}