import { Box, IconButton, ImageList, ImageListItem, ImageListItemBar, Typography, useMediaQuery, useTheme } from "@mui/material";
import SortIcon from '@mui/icons-material/Sort';
import SubdirectoryArrowRightIcon from '@mui/icons-material/SubdirectoryArrowRight';
import HomeScienceImage from '../../assets/images/projectImages/home-science.jpg';
import HomeScienceImage1 from '../../assets/images/projectImages/home-science1.jpg';
import Image1 from '../../assets/images/projectImages/residentcare-2.jpg';
import Image2 from '../../assets/images/projectImages/residentcare-1.jpg';
import Image3 from '../../assets/images/projectImages/home-training1.jpg';
import Image4 from '../../assets/images/projectImages/therapies3.jpg';



export default function CareGroup() {

    const itemData = [
        {
            img: HomeScienceImage,
        },
        {
            img: HomeScienceImage1,
        },
        {
            img: Image1,
        },
        {
            img: Image2,
        },
        {
            img: Image3,
        },
        {
            img: Image4,
        },
    ]

    const theme = useTheme();
    const isSmScreen = useMediaQuery(theme.breakpoints.down('sm'));
    const isMdScreen = useMediaQuery(theme.breakpoints.up('md'));

    return (
        <>
            <Box component={"div"} mt={15} mx={{ md: 5, xs: 2 }}>
                <Box display={"flex"} alignItems={"center"} justifyContent={"center"} color={'#009CFF'}>
                    <SortIcon sx={{ transform: 'scaleX(-1)' }} />
                    <Typography sx={{
                        fontFamily: "Jost,sans-serif",
                        fontWeight: 'bold',
                        textTransform: 'uppercase',
                        marginX: 2,
                    }}>
                        care
                    </Typography>
                    <SortIcon />
                </Box>
                <Typography sx={{
                    textAlign: "center",
                    fontFamily: "Jost,sans-serif",
                    fontWeight: 'bold',
                    fontSize: {
                        xs: "2rem",
                        sm: "2.5rem"
                    }
                }}>
                    Care Group
                </Typography>
                
                <Box component={"div"} my={5} color={"silver"}>
                    <Typography variant="body1" style={{ display: 'flex', alignItems: 'center', fontFamily: "Jost,sans-serif", marginTop: '1rem' }}>
                        <span style={{ marginLeft: '0.5rem' }}>
                            The severely retarded adults are separated from the trainable ones. When a separate class of same level of students is made, the goals set for them are similar. The teacher is able to take activities relevant to them, concentrate on them better. These adults can thus be taught activities or kept engaged as per their capacity and can benefit from the training and show good results.
                        </span>
                    </Typography>
                </Box>

                <ImageList sx={{ mt: 5 }} cols={isMdScreen ? 3 : isSmScreen ? 1 : 2}>
                    {itemData.map((item) => (
                        <ImageListItem key={item.img}>
                            <img
                                srcSet={`${item.img}?w=248&fit=crop&auto=format&dpr=2 2x`}
                                src={`${item.img}?w=248&fit=crop&auto=format`}
                                alt={item.title}
                                loading="lazy"
                            />
                        </ImageListItem>
                    ))}
                </ImageList>
            </Box>
        </>
    )
}